import React, { useMemo } from "react";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import WallDashboard from "./WallDashboard";
import EnableAnalytics from '../../../app/EnableAnalytics'



export function Dashboard(props) {
    const { analyticsStatus } = useSelector(
        ({ analyticsStatus }) => ({
            analyticsStatus: analyticsStatus
        }),
        shallowEqual
    );
    return (analyticsStatus && analyticsStatus.analytics) ? <WallDashboard {...props} analyticsStatus={analyticsStatus} /> : <EnableAnalytics analyticsStatus={analyticsStatus} />
}
