import React from "react";
import { Button, Modal } from "react-bootstrap";
import SVG from "react-inlinesvg";
import DataLoading from '../loader'
import { toAbsoluteUrl } from "../../_helpers";
import { kFormatter } from '../../../utils'
import { userInfluentialContributors } from '../../../actions/analytics'
import { paginate } from '../../../utils/paginate'
import TextContent from '../textContent'
import { UserProfileModalSkeletonCard } from "../skeleton-loader-cards/PopupSkeletonCards";

export default class UserProfileModal extends React.Component {
    state = {
        profileData: null,
        isLoading: true,
        total: 0,
        limit: 10,
        offset: 0,
        paginateData: {},
        currentPage: 1,
        maxPage: 10
    }
    componentDidMount() {
        this.onLoadData();

    }
    onLoadData = () => {
        const { userData } = this.props;
        const { limit, currentPage, maxPage, offset } = this.state;
        if (userData && Object.keys(userData).length > 0 && userData.author.username) {
            this.setState({ isLoading: true })
            userInfluentialContributors(userData.author.username, limit, offset).then(({ data }) => {
                this.setState({ profileData: data, total: data.total, paginateData: paginate(data.total, currentPage, limit, maxPage), isLoading: false })
            })
                .catch((error) => {
                    console.error("LAYOUT ISSUE", error)
                });
        }
    }
    onLoadDataWithPaginate = () => {
        const { userData } = this.props;
        const { limit, currentPage, maxPage, offset } = this.state;
        if (userData && Object.keys(userData).length > 0 && userData.author.username) {
            this.setState({ isLoading: true })
            userInfluentialContributors(userData.author.username, limit, offset).then(({ data }) => {
                this.setState({ profileData: data, isLoading: false })
            })
                .catch((error) => {
                    console.error("LAYOUT ISSUE", error)
                });
        }
    }
    onUpdateDataLimit = event => {
        const { total, currentPage, limit, maxPage } = this.state;
        const pageCount = parseInt(currentPage) == 1 ? 0 : parseInt(currentPage);
        const offset = parseInt(event.target.value) * pageCount;
        this.setState({ limit: event.target.value, paginateData: paginate(total, currentPage, event.target.value, maxPage), offset: offset }, () => this.onLoadDataWithPaginate())
    }
    onUpdatePage = (page) => event => {
        const { total, limit, maxPage } = this.state;
        const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
        const offset = parseInt(limit) * pageCount;
        this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadDataWithPaginate())

    }
    onPrevNavigateClick = event => {
        const { total, limit, maxPage, paginateData } = this.state;
        let page = 1;
        if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
            page = (paginateData.currentPage - 1)
        }
        if (page && page > 0) {
            const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
            const offset = parseInt(limit) * pageCount;
            this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadDataWithPaginate())
        }
    }
    onNextNavigateClick = event => {
        const { total, limit, maxPage, paginateData } = this.state;
        let page = 1;
        if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
            page = (paginateData.currentPage + 1)
        }
        if (page && paginateData && Object.keys(paginateData).length > 0 && paginateData.totalPages >= page) {
            const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
            const offset = parseInt(limit) * pageCount;
            this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadDataWithPaginate())
        }
    }
    render() {
        const { isLoading, profileData, total, limit, paginateData, } = this.state;
        const { showHide, handleClose, size, userData, trialAccessAPI } = this.props;
        return (
            <Modal show={showHide}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
                aria-labelledby="contained-modal-title-vcenter"
                centered
                size={size}>
                <Modal.Header className="justify-content-end border-0" closeButton></Modal.Header>
                <Modal.Body className="p-md-7 p-3">
                    {
                        (!isLoading && profileData && Object.keys(profileData).length > 0) ? <div className="user_details_section profile_with_users_list">
                            <div className="row">
                                <div className="col-lg-10 col-md-9 col-10 user_details_inner">
                                    <div className="row align-items-sm-center">
                                        <div className="col-lg-1 col-md-2 col-3 user_images mr-lg-8">
                                            <span className="no-repeat-fit-background" style={{ backgroundImage: `url(${profileData.authorImage.replace(/\s/g, '')}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span>
                                        </div>
                                        <div className="col-lg-10 col-md-10 col-8 user_info w-100">
                                            <h6 className="m-0 d-flex align-items-center flex-wrap">
                                                <span className="overflow-hidden text-nowrap text-ellipsis">{profileData.authorName}</span>
                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                    `/media/svg/social-icons/${userData.network.icon}.svg`
                                                )}
                                                ></SVG>
                                                <small className="overflow-hidden text-nowrap text-ellipsis post_count ml-lg-5 ml-md-2 ml-0">
                                                    <strong>{kFormatter(profileData.postsCount)}</strong> {profileData.postsCount && parseInt(profileData.postsCount) > 1 ? `Posts` : `Post`}</small>
                                                <small className="overflow-hidden text-nowrap text-ellipsis ml-5">
                                                    <strong>{kFormatter(profileData.followersCount)}</strong> {profileData.followersCount && parseInt(profileData.followersCount) > 1 ? `Followers` : `Follower`} </small>
                                            </h6>
                                            <p><span>@{profileData.authorUsername}</span></p>
                                            {/* <p>A User-Generated Content Platform For Authentic, Creative, Result-Driven Marketing.</p> */}
                                        </div>
                                    </div>
                                </div>
                                <div className="col-lg-2 col-md-3 col-2 text-right">
                                    <Button variant="btn btn-primary btn-min-w font-size-0" className={trialAccessAPI ? `disabled` : ``} size="sm" onClick={!trialAccessAPI ? event => window.open(userData.links ? userData.links : userData.link, '_blank') : null}>
                                        <SVG className="h-75 align-self-end w-20 mr-1" src={toAbsoluteUrl("/media/svg/cheat_icon_white.svg")}></SVG>
                                Connect</Button>
                                </div>
                            </div>
                            <div className="users_list mt-7">{
                                (profileData.responseData && profileData.responseData.length > 0) && profileData.responseData.map((pItem, pIndex) => <div className="card px-sm-5 px-2 py-sm-3 py-2 mb-3" key={pItem.id}>
                                    <div className="row">

                                        {pItem.type != 1 ? <div className="col-lg-4 user_profile_items cursor-pointer">
                                            <img src={pItem.mediaUrl ? pItem.mediaUrl : 'https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg'} className="img-fluid w-100 bg-light" onError={(e) => e.target.src = "https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg"} />
                                        </div> : null}


                                        <div className={`col-lg-${pItem.type === 1 ? 12 : 8} user_profile_items_info pl-lg-5 mt-lg-0 mt-5`}>
                                            <h6 className="m-0 d-flex justify-content-between user_profile_items_title">
                                                <strong className="overflow-hidden text-nowrap text-ellipsis">{pItem.author.name}</strong>
                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                    `/media/svg/social-icons/${pItem.network.icon}.svg`
                                                )}
                                                ></SVG>
                                            </h6>
                                            <p className="author_username"><span>@{pItem.author.username}</span></p>
                                            <div className="user_profile_others mt-lg-0 mt-2">
                                                <div className="row">
                                                <div className="col-sm-6">
                                                    <div className="user_profile_disc">
                                                        <TextContent fullContent={pItem.content} contentData={pItem.content} fullcontentData={pItem.content} />
                                                    </div>
                                                </div>
                                                <div className="col-sm-6">
                                                    <div className="user_profile_data ml-sm-7">
                                                    <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                                                        <span className="w-50 d-inline-block mr-1 text-nowrap">
                                                            <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                    `/media/svg/post-icon/impressions.svg`
                                                                )}
                                                                ></SVG>
                                                            </span> Impressions
                                                        </span>
                                                        <strong className="d-inline-block pl-sm-5">{kFormatter(pItem.Impressions)}</strong>
                                                    </p>
                                                    <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                                                        <span className="w-50 d-inline-block mr-1 text-nowrap">
                                                            <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                    `/media/svg/post-icon/post_click.svg`
                                                                )}
                                                                ></SVG>
                                                            </span> Post Clicks
                                                    </span>
                                                        <strong className="d-inline-block pl-sm-5">{kFormatter(pItem.postClick)}</strong>
                                                    </p>
                                                    <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                                                        <span className="w-50 d-inline-block mr-1 text-nowrap">
                                                            <span className="svg-icon svg-icon-sm svg-icon-dark-75">

                                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                    `/media/svg/post-icon/CTA.svg`
                                                                )}
                                                                ></SVG>
                                                            </span> Clicked CTA
                                                    </span>
                                                        <strong className="d-inline-block pl-sm-5">{kFormatter(pItem.postCTA)}</strong>
                                                    </p>
                                                    <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                                                        <span className="w-50 d-inline-block mr-1 text-nowrap">
                                                            <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                    `/media/svg/post-icon/total_reach.svg`
                                                                )}
                                                                ></SVG>

                                                            </span> Total Reach
                                                    </span>
                                                        <strong className="d-inline-block pl-sm-5">{kFormatter(pItem.potentialReach)}</strong>
                                                    </p>
                                                </div>
                                                </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>)
                            }

                            </div>
                        </div> : <UserProfileModalSkeletonCard />
                    }
                </Modal.Body>

                <Modal.Footer>
                    <>
                        {!isLoading && total > 0 && paginateData && Object.keys(paginateData).length > 0 ? <div className="w-100 d-flex justify-content-lg-between justify-content-center align-items-center flex-wrap px-lg-6 py-lg-4 px-sm-6 py-sm-4 px-0 pt-3 ">
                            <div className="d-flex flex-wrap py-2 mr-3 justify-content-sm-normal justify-content-center">

                                <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onPrevNavigateClick}>
                                    <i className="ki ki-bold-arrow-back icon-xs"></i>
                                </a>

                                {
                                    (paginateData.pages && paginateData.pages.length > 0) ? paginateData.pages.map((item, pagesIndex) => {
                                        return (parseInt(pagesIndex) < 5) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : (parseInt(paginateData.pages.length) - 2 == pagesIndex) ? <a className="btn btn-icon btn-sm border-0 btn-hover-primary mr-2 my-1">...</a> : (parseInt(paginateData.pages.length) - 1 == pagesIndex) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : null

                                    }) : null
                                }
                                <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onNextNavigateClick}>
                                    <i className="ki ki-bold-arrow-next icon-xs"></i>
                                </a>
                            </div>
                            <div className="d-flex align-items-center py-1 shwoing-filter">
                                <select
                                    className="form-control form-control-sm font-weight-bold mr-4 border-0 bg-light false"
                                    style={{ width: 75 }}
                                    onChange={this.onUpdateDataLimit}
                                    value={limit}
                                >

                                    <option className="" value={10}>10</option>
                                    <option className="" value={20}>20</option>
                                    <option className="" value={50}>50</option>
                                </select>
                                <span className="react-bootstrap-table-pagination-total">
                                    &nbsp;Showing rows {paginateData.currentPage} to&nbsp;{paginateData.totalPages} of&nbsp;{total}
                                </span>
                            </div>
                        </div> : null}

                    </>

                </Modal.Footer>

            </Modal>
        );
    }
}
