import React, { Component } from "react";
import { shallowEqual, useSelector, connect } from "react-redux";
import { TAGGBOX_USER_LOGIN } from '../actions/analytics'
import AnalyticStatus from './AnalyticStatus'
import { IS_LIVE } from '../actions/api'

class Routes extends Component {
    componentWillMount() {
        this.props.TAGGBOX_USER_LOGIN();
    }

    render() {
        const { authorized, wall } = this.props;
        var isAuthorized = null;
        if (IS_LIVE) {
            isAuthorized = authorized.isAuthorized;
            if (!authorized.isAuthorized) {
                // window.location.href = `https://app.taggbox.com/commerce`
            }
            else isAuthorized = true;
        }
        else {
            isAuthorized = true;
        }
        return isAuthorized ? <AnalyticStatus {...this.props} /> : null
    }
}
const mapStateToProps = (state) => {
    return {
        authorized: state.authorized,
        analyticsStatus: state.analyticsStatus,
        wall: state.wall
    }
}
const mapDispatchToProps = dispatch => {
    return {
        TAGGBOX_USER_LOGIN: () => dispatch(TAGGBOX_USER_LOGIN()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Routes);
