import React, { Component } from "react";
import { connect } from "react-redux";
import moment from 'moment'
import { v4 as uuidv4 } from 'uuid';
import SVG from "react-inlinesvg";
import { Button } from "react-bootstrap";
import { toAbsoluteUrl } from "../../_helpers";
import { kFormatter } from '../../../utils'
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { getwallRecordPostByMentionForPopUp } from '../../../actions/analytics'
import { paginate } from '../../../utils/paginate'


class MoreMentionPost extends Component {
  state = {
    record_post_by_mention: null,
    isLoading: true,
    total: 0,
    limit: 10,
    offset: 0,
    paginateData: {},
    currentPage: 1,
    maxPage: 10
  }

  componentDidMount() {
    const { record_post_by_mention, analyticwall } = this.props;
    const { limit, currentPage, maxPage } = this.state;
    //getwallRecordMostActiveUsersForPopUp()
    if (record_post_by_mention && record_post_by_mention.length > 0) {
      this.setState({ total: record_post_by_mention[0].total, paginateData: paginate(record_post_by_mention[0].total, currentPage, limit, maxPage) }, () => this.onLoadFilterData())
    }
  }
  onLoadFilterData = () => {
    const { analyticwall } = this.props;
    const { limit, offset } = this.state;
    const { selectedWallFeed, dateFilter, selectedFeed } = analyticwall;

    let wallData = null;
    if (selectedWallFeed && selectedWallFeed.wall) {
      wallData = selectedWallFeed.wall.id;
    }
    let startDate = dateFilter.startDate;
    let endDate = dateFilter.endDate;

    let feedIDs = [];
    if (selectedFeed && selectedFeed.length > 0) {
      selectedFeed.map((fItem) => {
        feedIDs.push(fItem.value);
      })
    }
    this.setState({ isLoading: true })
    getwallRecordPostByMentionForPopUp(wallData, moment(startDate).format("YYYY-MM-DD HH:mm:ss"), moment(endDate).format("YYYY-MM-DD HH:mm:ss"), feedIDs.join(), limit, offset).then((response) => {
      const { responseData } = response.data;
      this.setState({ record_post_by_mention: responseData, isLoading: false })
    })
      .catch((error) => {
        console.error("LAYOUT ISSUE", error)
      });

  }
  onUpdateDataLimit = event => {
    const { total, currentPage, limit, maxPage } = this.state;
    const pageCount = parseInt(currentPage) == 1 ? 0 : parseInt(currentPage);
    const offset = parseInt(event.target.value) * pageCount;
    this.setState({ limit: event.target.value, paginateData: paginate(total, currentPage, event.target.value, maxPage), offset: offset }, () => this.onLoadFilterData())
  }
  onUpdatePage = (page) => event => {
    const { total, limit, maxPage } = this.state;
    const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
    const offset = parseInt(limit) * pageCount;
    this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())

  }
  onPrevNavigateClick = event => {
    const { total, limit, maxPage, paginateData } = this.state;
    let page = 1;
    if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
      page = (paginateData.currentPage - 1)
    }
    if (page && page > 0) {
      const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
      const offset = parseInt(limit) * pageCount;
      this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())
    }
  }
  onNextNavigateClick = event => {
    const { total, limit, maxPage, paginateData } = this.state;
    let page = 1;
    if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
      page = (paginateData.currentPage + 1)
    }
    if (page && paginateData && Object.keys(paginateData).length > 0 && paginateData.totalPages >= page) {
      const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
      const offset = parseInt(limit) * pageCount;
      this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())
    }
  }

  render() {
    const { className } = this.props;
    const { isLoading, record_post_by_mention, total, limit, paginateData, currentPage } = this.state;


    return (
      <>
        <div className="modal_wrapper">
          {
            isLoading ? <div className="h-100"><DataLoading /></div> : record_post_by_mention[0].mentionsPosts && record_post_by_mention[0].mentionsPosts.length > 0 ? record_post_by_mention[0].mentionsPosts.map((mItem, mIndex) => <div className="d-flex align-items-center px-3 px-xxl-6 py-3" key={uuidv4()}>
              <div className="symbol symbol-60 symbol-2by2 flex-shrink-0 mr-4">
                <div className="symbol-label rounded-0 no-repeat-fit-background" style={{ backgroundImage: `url(${mItem.image}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></div>
                <i className="icon_on_img">
                  <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                    `/media/svg/social-icons/${mItem.network.icon}.svg`
                  )}
                  ></SVG>
                </i>
              </div>
              <div className="d-flex flex-column flex-grow-1">
                <span className="text-dark font-weight-normal font-size-sm overflow-hidden text-ellipsis line-clamp-3">
                  {mItem.content}</span>
              </div>
            </div>
            ) : <DataNotFound />
          }
        </div>
        <>
          {!isLoading && total > 0 && paginateData && Object.keys(paginateData).length > 0 ? <div className="d-flex justify-content-between align-items-center flex-wrap px-6 py-4">
            <div className="d-flex flex-wrap py-2 mr-3">

              <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onPrevNavigateClick}>
                <i className="ki ki-bold-arrow-back icon-xs"></i>
              </a>

              {
                (paginateData.pages && paginateData.pages.length > 0) ? paginateData.pages.map((item, pagesIndex) => {
                  return (parseInt(pagesIndex) < 5) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : (parseInt(paginateData.pages.length) - 2 == pagesIndex) ? <a className="btn btn-icon btn-sm border-0 btn-hover-primary mr-2 my-1">...</a> : (parseInt(paginateData.pages.length) - 1 == pagesIndex) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : null

                }) : null
              }
              <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onNextNavigateClick}>
                <i className="ki ki-bold-arrow-next icon-xs"></i>
              </a>
            </div>
            <div className="d-flex align-items-center py-1">
              <select
                className="form-control form-control-sm font-weight-bold mr-4 border-0 bg-light false"
                style={{ width: 75 }}
                onChange={this.onUpdateDataLimit}
                value={limit}
              >

                <option className="btn " value={10}>10</option>
                <option className="btn " value={20}>20</option>
                <option className="btn " value={50}>50</option>
              </select>
              <span className="react-bootstrap-table-pagination-total">
                &nbsp;Showing rows {paginateData.currentPage} to&nbsp;{paginateData.totalPages} of&nbsp;{total}
              </span>
            </div>
          </div> : null}

        </>
      </>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    analyticwall: state.analyticwall
  }
}
export default connect(mapStateToProps)(MoreMentionPost);