/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import { connect } from "react-redux";
import { OverviewGraph } from './OverviewGraph'
import { OverviewChartFormatting } from '../../../utils'
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { OverviewSkeleton } from "../skeleton-loader-cards/SkeletonCards";
class Overview extends Component {
  state = {
    record_overview: null,
    isLoading: true
  }
  componentDidMount() {
    const { record_overview } = this.props;
    if (record_overview) {
      this.setState({ record_overview: record_overview }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
    }
  }
  static getDerivedStateFromProps(props, state) {
    if (props.record_overview !== state.record_overview) {
      return {
        record_overview: props.record_overview
      };
    }
    return null;
  }
  render() {

    const { isLoading, record_overview } = this.state;
    const { pageRequest } = this.props
    return (
      <div className="card card-custom card-stretch gutter-b">
        {pageRequest.loader ? <OverviewSkeleton /> : <>
          <div className="card-header min-h-auto py-3">
            <div className="card-title">
              <h5 className="card-label font-weight-bolder">Overview</h5>
            </div>
          </div>
          <div className="card-body">
            {pageRequest.loader ? <DataLoading /> : record_overview && record_overview.length > 0 ? <OverviewGraph data={OverviewChartFormatting(record_overview)} record_overview={record_overview} /> : <DataNotFound />}
          </div>
        </>}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    record_overview: state.wallRecordOverview.record_overview,
    pageRequest: state.wallRecordOverview
  }
}
export default connect(mapStateToProps)(Overview);