import React, { Component } from "react";
import { connect } from "react-redux";
import { v4 as uuidv4 } from 'uuid';
import {
  Button,
  Overlay,
  Tooltip,
  OverlayTrigger,
  ButtonToolbar
} from "react-bootstrap";
import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../_helpers";
import DataLoading from '../loader'
import { kFormatter } from '../../../utils'
import { CountersSkeleton } from "../skeleton-loader-cards/SkeletonCards";



class Counters extends Component {
  state = {
    record_counts: null,
    isLoading: true
  }
  componentDidMount() {
    const { record_counts } = this.props;
    if (record_counts) {
      this.setState({ record_counts: record_counts }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
    }
  }
  static getDerivedStateFromProps(props, state) {
    if (props.record_counts !== state.record_counts) {
      return {
        record_counts: props.record_counts
      };
    }
    return null;
  }


  render() {
    const { isLoading, record_counts } = this.state;
    const { pageRequest } = this.props
    return (
      <div className="row">
        {pageRequest.loader ? <CountersSkeleton /> :
          <div className="col-sm-6 col-md-6 col-xxl-3">
            <div className="card card-custom card-stretch gutter-b d-flex align-items-start flex-nowrap flex-row justify-content-between flex-fill p-6">
              <div className="d-flex flex-column">
                <span className="font-weight-bold font-size-h5 text-dark mb-3">
                  {pageRequest.loader ? null : record_counts.length > 0 && record_counts[0] ? record_counts[0].label : null}
                </span>
                <span className="font-weight-boldest text-dark display-4 mb-lg-3">{pageRequest.loader ? <DataLoading /> : record_counts.length > 0 && record_counts[0] ? kFormatter(record_counts[0].total) : 0}</span>
              </div>
              {pageRequest.loader ? null :
                <div className="">
                  <OverlayTrigger
                    placement='top'
                    overlay={
                      <Tooltip>{record_counts.length > 0 && record_counts[0] ? record_counts[0].tool_tip : ""}</Tooltip>
                    }
                  >
                    <SVG
                      src={toAbsoluteUrl(
                        "/media/svg/question.svg"
                      )}
                    ></SVG>
                  </OverlayTrigger>
                </div>
              }
            </div>
          </div>}
        {pageRequest.loader ? <CountersSkeleton /> :
          <div className="col-sm-6 col-md-6 col-xxl-3">
            <div className="card card-custom card-stretch gutter-b d-flex align-items-start flex-nowrap flex-row justify-content-between flex-fill p-6">
              <div className="d-flex flex-column">
                <span className="font-weight-bold font-size-h5 text-dark mb-3">
                  {pageRequest.loader ? null : record_counts.length > 0 && record_counts[1] ? record_counts[1].label : ""}
                </span>
                <span className="font-weight-boldest text-dark display-4 mb-lg-3">{pageRequest.loader ? <DataLoading /> : record_counts.length > 0 && record_counts[1] ? kFormatter(record_counts[1].total) : 0}</span>
              </div>
              {
                pageRequest.loader ? null :
                  <div className="">
                    <OverlayTrigger
                      placement='top'
                      overlay={
                        <Tooltip>{record_counts.length > 0 && record_counts[1] ? record_counts[1].tool_tip : ""}</Tooltip>
                      }
                    >
                      <SVG
                        src={toAbsoluteUrl(
                          "/media/svg/question.svg"
                        )}
                      ></SVG>
                    </OverlayTrigger>
                  </div>
              }
            </div>
          </div>}
        {pageRequest.loader ? <CountersSkeleton /> :
          <div className="col-sm-6 col-md-6 col-xxl-3">
            <div className="card card-custom card-stretch gutter-b d-flex align-items-start flex-nowrap flex-row justify-content-between flex-fill p-6">
              <div className="d-flex flex-column">
                <span className="font-weight-bold font-size-h5 text-dark mb-3">
                  {pageRequest.loader ? null : record_counts.length > 0 && record_counts[2] ? record_counts[2].label : ""}
                </span>
                <span className="font-weight-boldest text-dark display-4 mb-lg-3">{pageRequest.loader ? <DataLoading /> : record_counts.length > 0 && record_counts[2] ? kFormatter(record_counts[2].total) : 0}</span>
              </div>
              {
                pageRequest.loader ? null :
                  <div className="">
                    <OverlayTrigger
                      placement='top'
                      overlay={
                        <Tooltip> {record_counts.length > 0 && record_counts[2] ? record_counts[2].tool_tip : ""}</Tooltip>
                      }
                    >
                      <SVG
                        src={toAbsoluteUrl(
                          "/media/svg/question.svg"
                        )}
                      ></SVG>
                    </OverlayTrigger>
                  </div>
              }
            </div>
          </div>}
        {pageRequest.loader ? <CountersSkeleton /> :
          <div className="col-sm-6 col-md-6 col-xxl-3">
            <div className="card card-custom card-stretch gutter-b d-flex align-items-start flex-nowrap flex-row justify-content-between flex-fill p-6">
              <div className="d-flex flex-column">
                <span className="font-weight-bold font-size-h5 text-dark mb-3">
                  {pageRequest.loader ? null : record_counts.length > 0 && record_counts[3] ? record_counts[3].label : ""}
                </span>
                <span className="font-weight-boldest text-dark display-4 mb-lg-3">{pageRequest.loader ? <DataLoading /> : record_counts.length > 0 && record_counts[3] ? kFormatter(record_counts[3].total) : 0}</span>
              </div>
              {
                pageRequest.loader ? null :
                  <div className="">
                    <OverlayTrigger
                      placement='top'
                      overlay={
                        <Tooltip> {record_counts.length > 0 && record_counts[3] ? record_counts[3].tool_tip : ""}</Tooltip>
                      }
                    >
                      <SVG
                        src={toAbsoluteUrl(
                          "/media/svg/question.svg"
                        )}
                      ></SVG>
                    </OverlayTrigger>
                  </div>
              }
            </div>
          </div>}
      </div>
    );
  }

}
const mapStateToProps = (state) => {
  return {
    record_counts: state.wallRecordCounts.record_counts,
    pageRequest: state.wallRecordCounts
  }
}

export default connect(mapStateToProps)(Counters);