import {
    API_GET_WALL_RECORD_INFLUENTIAL_CONTRIBUTORS_LOADER,
    GET_WALL_RECORD_INFLUENTIAL_CONTRIBUTORS,
} from './keys'
const initialState = {
    record_influential_contributors: [],
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_INFLUENTIAL_CONTRIBUTORS: {
            return { ...state, record_influential_contributors: action.payload }
        }
        case API_GET_WALL_RECORD_INFLUENTIAL_CONTRIBUTORS_LOADER: {
            return { ...state, loader: action.payload}
        }
        default:
            return { ...state }
    }

}