import {
    API_GET_WALL_RECORD_POST_BY_MENTION_LOADER,
    GET_WALL_RECORD_POST_BY_MENTION
} from './keys'
const initialState = {
    record_post_by_mention: [],
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_POST_BY_MENTION: {
            return { ...state, record_post_by_mention: action.payload }
        }
        case API_GET_WALL_RECORD_POST_BY_MENTION_LOADER: {
            return { ...state, loader: action.payload}
        }
        default:
            return { ...state }
    }

}