import React, { Component } from "react";
import { Switch } from "react-router-dom";
import { connect } from "react-redux";
import { Layout } from "../_analytics/layout";
import BasePage from "./BasePage";
import NotFound from './error'
class RoutesExtends extends Component {
    render() {
        const { analyticwall, errorUpdate } = this.props;
        const { userfeeds, selectedFeed } = analyticwall;
        //&& (selectedFeed && selectedFeed.length > 0)
        return ((userfeeds && userfeeds.length > 0) && !errorUpdate) ? <Switch>
            <Layout {...this.props}>
                <BasePage {...this.props} />
            </Layout>
        </Switch> : errorUpdate ? <NotFound /> : null

    }
}
const mapStateToProps = (state) => {
    return {
        analyticsStatus: state.analyticsStatus,
        wall: state.wall,
        analyticwall: state.analyticwall,
        errorUpdate: state.errorUpdate.error
    }
}
const mapDispatchToProps = dispatch => {
    return {

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(RoutesExtends);


