/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React from "react";
import SVG from "react-inlinesvg";
import moment from 'moment';
import { toAbsoluteUrl } from "../../_helpers";
import { kFormatter } from '../../../utils'


export function TopPost(props) {
  const { data, style, IsOpenUserProfile, isPostOpen } = props;
  const icon = data.network.icon.includes("fa-") ? data.network.icon : `fa-${data.network.icon}`

  return (

    <div className="top_posts" style={style}>
      <div className="border rounded-sm p-4 mb-5">
        {/* <a href={data.link} target="_blank"> */}
        <div className="m_card_cont">
          <div className="d-flex align-items-center justify-content-between mb-4 cursor-pointer" onClick={IsOpenUserProfile(data)}>
            <div className="d-flex align-items-center m_grid_author">
              <div className="flex-shrink-0 mr-4 mt-lg-0 mt-3">
                <div className="symbol symbol-circle symbol-lg-75 m_profile_img no-repeat-fit-background" style={{ backgroundImage: `url(${data.author.image.replace(/\s/g, '')}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}>
                </div>
              </div>
              <div className="d-flex flex-column m_user_content">
                <a href="#" className="m_post_title no-pointer-events" >{data.author.name}</a>
                <div className="d-flex align-item-center m_grid_userinfo">
                  <span className="m_grid_uername text-nowrap"><span className="ng-binding">@{data.author.username}</span></span>
                  <span className="sepratedot"></span>

                  <span className="timePost text-nowrap">{moment(data.postTime).format("DD-MMM-YYYY")}</span>
                </div>
              </div>
            </div>
            <div className="d-flex flex-column m_social_icon ">
              <SVG className="perf_post_icon" src={toAbsoluteUrl(
                `/media/svg/social-icons/${icon}.svg`
              )}
              ></SVG>
            </div>
          </div>
          <div className={`post_cotnent cursor-pointer  ${data.type != 1 ? `` : `pt-4 pb-4`} `} onClick={isPostOpen(data)}>

            <p className={`text-dark font-weight-normal font-size-xs overflow-hidden text-ellipsis line-clamp-4 ${data.type != 1 ? `` : `pr-1 pl-1`}`}>{data.content}</p>
          </div>
          {data.type != 1 ?
            <div className="post_img1 mb-2 cursor-pointer" onClick={isPostOpen(data)}>
              <img className="img-fluid rounded-sm" src={data.mediaUrl} onError={(e) => e.target.src = "https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg"} />
            </div>
            : null
          }
          <div className="post_score">
            <div className="d-flex align-items-center justify-content-between py-3">
              <span className="text-dark font-weight-bold font-size-md">Impressions</span>
              <h6 className="text-dark font-weight-boldest mb-0">{kFormatter(parseInt(data.Impressions))}</h6>
            </div>
            <div className="d-flex align-items-center justify-content-between py-3">
              <span className="text-dark font-weight-bold font-size-md">Post Clicks</span>
              <h6 className="text-dark font-weight-boldest mb-0">{kFormatter(parseInt(data.postClick))}</h6>
            </div>
            <div className="d-flex align-items-center justify-content-between py-3">
              <span className="text-dark font-weight-bold font-size-md">Clicked CTA</span>
              <h6 className="text-dark font-weight-boldest mb-0">{kFormatter(parseInt(data.postCTA))}</h6>
            </div>
            <div className="d-flex align-items-center justify-content-between py-3">
              <span className="text-dark font-weight-bold font-size-md">Post Share Count</span>
              <h6 className="text-dark font-weight-boldest mb-0">{data.showShareCount == 0 ? `N/A` : data.showShareCount == 1 ? kFormatter(parseInt(data.potentialReach)) : 0}</h6>
            </div>
          </div>
        </div>
        {/* </a> */}
      </div>
    </div>


  );
}
