import React from "react";
import Chart from 'react-apexcharts'
export function OverviewGraph(props) {
    let labels = [];
    if (props.record_overview && props.record_overview.length > 0) {
        labels = props.record_overview[0].labels;
    }
    const data = {
        options: {
            chart: {
                id: 'analytics_record_overview_chart'
            },
            xaxis: {
                categories: labels,
            },
            yaxis: [
                {
                    labels: {
                        formatter: (val) => {
                            return (val) ? val.toFixed(0) : 0
                        }
                    }
                }
            ],
            colors: ['#7647FD', '#FB0B95', '#5096FF', '#FEC736'],
            tooltip: {
                marker: {
                    show: true,
                    radius: 2
                }
            },
            legend: {
                offsetX: -10,
                offsetY: 0,
                markers: {
                    width: 14,
                    height: 14,
                    radius: 2,
                    offsetX: -5,
                    offsetY: 0
                },
                itemMargin: {
                    horizontal: 25,
                    vertical: 10
                }
            }
        },
        series: props.data,
    };
    return <div id="analytics_update_record_overview_chart">
        <Chart options={data.options} series={data.series} type="line" height={350} />
    </div>
}


