import React, { Component } from "react";
import { v4 as uuidv4 } from 'uuid';
import DataNotFound from '../notfound'
import {
    Button,
    Overlay,
    Tooltip,
    OverlayTrigger,
    Tab, Tabs, Nav,
    ButtonToolbar
} from "react-bootstrap";
import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../_helpers";
import DataLoading from '../loader'
import MentionPostsModal from "../mention-posts-modal/mentionPostsModal";

class SentimentsPost extends Component {
    state = {
        Data: null,
        isLoading: true,
        isPost: false,
        postData: null
    }
    componentDidMount() {
        const { Data } = this.props;
        if (Data) {
            this.setState({ Data: Data }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
        }
    }
    static getDerivedStateFromProps(props, state) {
        if (props.Data !== state.Data) {
            return {
                Data: props.Data
            };
        }
        return null;
    }
    isPostOpen = (data) => event => {
        this.setState({ isPost: true, postData: { ...data, id: data.postPrimaryId }, show: false });
    }
    isPostClose = event => this.setState({ isPost: false, postData: null });
    render() {
        const { Data, isLoading } = this.state;
        const { analyticsStatus } = this.props
        return <>
            <Tabs defaultActiveKey="mentions_posts" id="sentiment-postgraph" className="nav-tabs-line text-dark font-size-h5 flex-nowrap overflow-x-auto">
                <Tab eventKey="mentions_posts" title="Positive">
                    <div className="card-body p-0 mb-5">
                        {
                            isLoading ? <DataLoading /> : Data.positive && Data.positive.length > 0 ? Data.positive.map((mItem, mIndex) => <div key={uuidv4()} className="d-flex align-items-center px-5 px-xxl-5 py-3 cursor-pointer" onClick={this.isPostOpen(mItem)}>

                                <div className="symbol symbol-45 symbol-light mr-sm-6 mr-3 con_user_img">
                                    <span className="no-repeat-fit-background" style={{ backgroundImage: `url(${mItem.picture.replace(/\s/g, '')}) ,url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span>
                                    <i className="icon_on_img">
                                        <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                            `/media/svg/social-icons/${mItem.icon}.svg`
                                        )}
                                        ></SVG>
                                    </i>
                                </div>
                                <div className="d-flex flex-column flex-grow-1">
                                    <span className="text-dark font-weight-normal font-size-sm overflow-hidden text-ellipsis line-clamp-2">
                                        {mItem.content}</span>
                                </div>
                            </div>
                            ) : <DataNotFound />
                        }

                    </div>
                </Tab>
                <Tab eventKey="shared_posts" title="Negative">
                    <div className="card-body p-0 mb-5">
                        {
                            isLoading ? <DataLoading /> : Data.negative && Data.negative.length > 0 ? Data.negative.map((mItem, mIndex) => <div key={uuidv4()} className="d-flex align-items-center px-5 px-xxl-5 py-3 cursor-pointer" onClick={this.isPostOpen(mItem)}>
                                <div className="symbol symbol-45 symbol-light mr-sm-6 mr-3 con_user_img">

                                    <span className="no-repeat-fit-background" style={{ backgroundImage: `url(${mItem.picture.replace(/\s/g, '')}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span>
                                    <i className="icon_on_img">
                                        <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                            `/media/svg/social-icons/${mItem.icon}.svg`
                                        )}
                                        ></SVG>
                                    </i>
                                </div>
                                <div className="d-flex flex-column flex-grow-1">
                                    <span className="text-dark font-weight-normal font-size-sm overflow-hidden text-ellipsis line-clamp-2">
                                        {mItem.content}</span>
                                </div>
                            </div>
                            ) : <DataNotFound />} </div>

                </Tab>
            </Tabs>

            {this.state.isPost ? <MentionPostsModal title={`Sentiment Posts`} size={'lg'} show={this.state.isPost} onHide={this.isPostClose} userData={this.state.postData} trialAccessAPI={analyticsStatus.trialAccessAPI} /> : null}
        </>
    }
}

export default SentimentsPost


