import React, { Component } from "react";
import { connect } from "react-redux";
import moment from 'moment'
import Loader from 'react-loader-spinner';
import saveAs from 'file-saver';
import { v4 as uuidv4 } from 'uuid';
import SVG from "react-inlinesvg";
import ReactToPdf from 'react-to-pdf'
import { Form } from 'react-bootstrap';
import { Button } from "react-bootstrap";
import { toAbsoluteUrl } from "../../_helpers";
import { kFormatter } from '../../../utils'
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { getwallRecordMostActiveUsersForPopUp, pdfTopMostActiveGenerate } from '../../../actions/analytics'
import { paginate } from '../../../utils/paginate'
import { SeeMoreContributorsSkeletonCard } from "../skeleton-loader-cards/PopupSkeletonCards";



class MoreActiveUsers extends Component {
  state = {
    record_most_active_users: null,
    isLoading: true,
    total: 0,
    limit: 10,
    offset: 0,
    paginateData: {},
    currentPage: 1,
    maxPage: 10,
    isPdfDownloading: false,
    username: null,
    postType: 0,
  }

  componentDidMount() {
    const { record_most_active_users, analyticwall, analyticsStatus } = this.props;
    const { limit, currentPage, maxPage } = this.state;
    if (record_most_active_users && record_most_active_users.data) {
      if (analyticsStatus.trialAccessAPI) this.setState({ total: 10, paginateData: paginate(10, currentPage, limit, maxPage) }, () => this.onLoadFilterData())
      else this.setState({ total: record_most_active_users.total, paginateData: paginate(record_most_active_users.total, currentPage, limit, maxPage) }, () => this.onLoadFilterData())
    }
    this.onSearchText()
  }
  onLoadFilterData = () => {
    const { analyticwall , analyticsStatus} = this.props;
    const { limit, offset, username ,postType} = this.state;
    const { selectedWallFeed, dateFilter, selectedFeed } = analyticwall;

    let wallData = null;
    if (selectedWallFeed && selectedWallFeed.wall) {
      wallData = selectedWallFeed.wall.id;
    }
    let startDate = dateFilter.startDate;
    let endDate = dateFilter.endDate;

    let feedIDs = [];
    if (selectedFeed && selectedFeed.length > 0) {
      selectedFeed.map((fItem) => {
        feedIDs.push(fItem.value);
      })
    }
    this.setState({ isLoading: true })
    getwallRecordMostActiveUsersForPopUp(wallData, moment(startDate).format("YYYY-MM-DD HH:mm:ss"), moment(endDate).format("YYYY-MM-DD HH:mm:ss"), feedIDs.join(), limit, offset, postType,username).then((response) => {
      const { responseData } = response.data;
      if (analyticsStatus.trialAccessAPI) this.setState({ total: responseData.total, record_most_active_users: responseData.data, isLoading: false })
      else this.setState({ record_most_active_users: responseData.data, isLoading: false })
    })
      .catch((error) => {
        console.error("LAYOUT ISSUE", error)
      });

  }
  onLoadFilterDataTextSearch = () => {
    const { analyticwall, total, analyticsStatus } = this.props;
    const { limit, offset, username, currentPage, maxPage, postType } = this.state;
    const { selectedWallFeed, dateFilter, selectedFeed } = analyticwall;
    let wallData = null;
    if (selectedWallFeed && selectedWallFeed.wall) {
      wallData = selectedWallFeed.wall.id;
    }
    let startDate = dateFilter.startDate;
    let endDate = dateFilter.endDate;
    let feedIDs = [];
    if (selectedFeed && selectedFeed.length > 0) {
      selectedFeed.map((fItem) => {
        feedIDs.push(fItem.value);
      })
    }
    this.setState({ isLoading: true })
    getwallRecordMostActiveUsersForPopUp(wallData, moment(startDate).format("YYYY-MM-DD HH:mm:ss"), moment(endDate).format("YYYY-MM-DD HH:mm:ss"), feedIDs.join(), username && username.length > 0 ? analyticsStatus.trialAccessAPI ? this.state.total : total : limit, postType, username && username.length > 0 ? 0 : offset, username).then((response) => {
      const { responseData } = response.data;
      this.setState({ total: responseData.total, paginateData: paginate(responseData.total, currentPage, limit, maxPage), record_most_active_users: responseData.data, isLoading: false })
    })
      .catch((error) => {
        console.error("LAYOUT ISSUE", error)
      });
  }
  onSearchText = () => {
    let textInput = document.getElementById('searchTextMostActive');
    let timeout = null;
    textInput.addEventListener('keyup', (e) => {
      clearTimeout(timeout);
      timeout = setTimeout(() => {
        this.setState({ username: textInput.value }, () => this.onLoadFilterDataTextSearch())
      }, 1000);
    });
  }

  onUpdateDataLimit = event => {
    const { total, currentPage, limit, maxPage } = this.state;
    const pageCount = parseInt(currentPage) == 1 ? 0 : parseInt(currentPage);
    const offset = parseInt(event.target.value) * pageCount;
    this.setState({ limit: event.target.value, paginateData: paginate(total, currentPage, event.target.value, maxPage), offset: offset }, () => this.onLoadFilterData())
  }
  onUpdatePage = (page) => event => {
    const { total, limit, maxPage } = this.state;
    const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
    const offset = parseInt(limit) * pageCount;
    this.setState({ currentPage: page, paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())

  }
  onPrevNavigateClick = event => {
    const { total, limit, maxPage, paginateData } = this.state;
    let page = 1;
    if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
      page = (paginateData.currentPage - 1)
    }
    if (page && page > 0) {
      const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
      const offset = parseInt(limit) * pageCount;
      this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())
    }
  }
  onNextNavigateClick = event => {
    const { total, limit, maxPage, paginateData } = this.state;
    let page = 1;
    if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
      page = (paginateData.currentPage + 1)
    }
    if (page && paginateData && Object.keys(paginateData).length > 0 && paginateData.totalPages >= page) {
      const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
      const offset = parseInt(limit) * pageCount;
      this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())
    }
  }

  clickToExport = event => {
    const { limit, maxPage, paginateData, username } = this.state;
    let page = 1;
    if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
      page = (paginateData.currentPage - 1)
    }
    const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
    const offset = parseInt(limit) * pageCount;
    this.setState({ isPdfDownloading: true })
    pdfTopMostActiveGenerate(username, offset, limit).then((response) => {
      const file = new Blob([response.data], { type: 'application/pdf' });
      //const fileURL = URL.createObjectURL(file);
      saveAs(file, 'Most_Active_Creators.pdf');
      this.setState({ isPdfDownloading: false })
    }).catch((error) => {
      console.error("pdfAnalyticGenerate issue", error)
      this.setState({ isPdfDownloading: false })
    });
  }


  render() {
   const { className, IsOpenUserProfile, analyticsStatus } = this.props;
    const { isLoading, record_most_active_users, total, limit, paginateData, currentPage, isPdfDownloading } = this.state;

    return <div className="influential_contributors_custom_modal px-5">
<div className={isLoading ? `d-none` : "d-flex align-items-center justify-content-end mb-3"}>
        <Button variant="outline-dark mr-4 px-4 px-sm-5 export-pdf" size="sm" onClick={!analyticsStatus.trialAccessAPI ? this.clickToExport : null} className={isPdfDownloading ? `d-flex pdf-loading` : `d-flex `, analyticsStatus.trialAccessAPI ? `disabled` : ``} disabled={isPdfDownloading ? `disabled` : ''}>
          <span className="svg-icon svg-icon-sm">
            <SVG className="h-75 align-self-end" src={toAbsoluteUrl(
              "/media/svg/export.svg"
            )}
            ></SVG>
          </span>
          <span className="btn_txt">{
            isPdfDownloading ? <Loader type="ThreeDots" color="#333333" height={25} width={25} style={{ marginRight: 5 }} /> : `Export`
          }</span>

        </Button>

        <Form>
          <Form.Group className="m-0">
            <div className="input-icon input-icon-right">
              <Form.Control className="search-input" id="searchTextMostActive" type="text" placeholder="Search Most Active Creators..." />
              <span>
                <i className="flaticon2-search-1 icon-md"></i>
              </span>
            </div>
          </Form.Group>
        </Form>
      </div>

      <div className="influential-data-list">
        {
            isLoading ? <SeeMoreContributorsSkeletonCard /> : (record_most_active_users && record_most_active_users.length > 0) ? record_most_active_users.map((mItem, mIndex) => <div className="card mb-3"> 
             <div className="d-flex align-items-center justify-content-between px-5 py-5 users_row mobile-w-100 mobile-d-block" key={uuidv4()}>
             


             <div className="d-flex flex-grow-1 mr-sm-6 w-50 mobile-w-100">
                <div className="symbol symbol-45 symbol-light mr-6 con_user_img cursor-pointer" onClick={IsOpenUserProfile(mItem)} >
                <span className="no-repeat-fit-background" style={{ backgroundImage: `url(${mItem.image.replace(/\s/g, '')}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span>
                 
                </div>
                <div className="d-flex text-left flex-column conn_user_info w-100">
                  <div className="d-flex">
                  <h6 className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name cursor-pointer" onClick={IsOpenUserProfile(mItem)}>{mItem.author.name}<br />
                      <span className="sub_author_name">@{mItem.author.username}</span>
                    </h6>
                    <span> <SVG className="h-auto align-self-end w-20" src={toAbsoluteUrl(
                      `/media/svg/social-icons/${mItem.network.icon}.svg`
                    )}></SVG></span>
                  </div>
                  <p className="m-0 sub_author_name">
                    {/* A User-Generated Content Platform For Authentic, Creative, Result-Driven Marketing. */}
                  </p>
                </div>
              </div>




                <div className="d-flex align-items-center w-50 justify-content-between mobile-w-100 mt-lg-0 mt-md-3 mt-5">
                <div className="mr-6">
                  <p className="mb-lg-3 mt-sm-2 mb-0"><strong className="text-dark">{kFormatter(mItem.postCount)}</strong> {mItem.postCount && parseInt(mItem.postCount) > 1 ? `Posts` : `Post`} </p>
                  <p className="mb-0"><strong className="text-dark">{kFormatter(mItem.author.follower)}</strong> {mItem.author.follower && parseInt(mItem.author.follower) > 1 ? `Followers` : `Follower`} </p>
                </div>

                <div className="conn_status connnected">
                  <div className="conn_status_wrap">
                    <Button variant="outline-dark btn-min-w" size="sm" className={analyticsStatus.trialAccessAPI ? `disabled` : ``} onClick={!analyticsStatus.trialAccessAPI ? event => window.open(mItem.links, '_blank') : null}>
                      <SVG className="h-75 align-self-end w-20 mr-1" src={toAbsoluteUrl("/media/svg/cheat_icon_black.svg")}></SVG> Connect</Button>
                  </div>
                </div>

              </div>
              
                </div>
            </div>
            ) : <DataNotFound />
          }
        </div>
        <>
          {!isLoading && total > 0 && paginateData && Object.keys(paginateData).length > 0 ? <div className="d-flex justify-content-between align-items-center flex-wrap px-6 py-4">
            <div className="d-flex flex-wrap py-2 mr-3">

              <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onPrevNavigateClick}>
                <i className="ki ki-bold-arrow-back icon-xs"></i>
              </a>
              {
                (paginateData.pages && paginateData.pages.length > 0) ? paginateData.pages.map((item, pagesIndex) => {

                  return (parseInt(pagesIndex) < 5) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : (parseInt(paginateData.pages.length) - 2 == pagesIndex) ? <a className="btn btn-icon btn-sm border-0 btn-hover-primary mr-2 my-1">...</a> : (parseInt(paginateData.pages.length) - 1 == pagesIndex) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : null

                }) : null
              }
              <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onNextNavigateClick}>
                <i className="ki ki-bold-arrow-next icon-xs"></i>
              </a>
            </div>
            <div className="d-flex align-items-center py-1">
              <select
                className="form-control form-control-sm font-weight-bold mr-4 border-0 bg-light false"
                style={{ width: 75 }}
                onChange={this.onUpdateDataLimit}
                value={limit}
              >
                <option className="btn" value={10}>10</option>
                <option className="btn" value={20}>20</option>
                <option className="btn" value={50}>50</option>
              </select>
              <span className="react-bootstrap-table-pagination-total">
                &nbsp;Showing rows {paginateData.currentPage} to&nbsp;{paginateData.totalPages} of&nbsp;{total}
              </span>
            </div>
          </div> : null}

        </>
        </div> 
  }
}
const mapStateToProps = (state) => {
  return {
    analyticwall: state.analyticwall,
    analyticsStatus: state.analyticsStatus,
  }
}
export default connect(mapStateToProps)(MoreActiveUsers);