import {
    API_GET_WALL_RECORD_SENTIMENT_BY_POST_STATISTICS_LOADER,
    GET_WALL_RECORD_SENTIMENT_BY_POST_STATISTICS
} from './keys'
const initialState = {
    record_sentiment_by_post_statistics: [],
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_SENTIMENT_BY_POST_STATISTICS: {
            return { ...state, record_sentiment_by_post_statistics: action.payload }
        }
        case API_GET_WALL_RECORD_SENTIMENT_BY_POST_STATISTICS_LOADER: {
        return { ...state, loader: action.payload}
        }
        default:
        return { ...state }
    }

}