import React, { Component } from "react";
import { connect } from "react-redux";
import { v4 as uuidv4 } from 'uuid';
import { TopPost } from "./TopPost";
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import SeeMoreModal from "../see-more-modal/SeeMoreModal";
import MoreTopPerformancePosts from './MoreTopPerformancePosts'
import UserProfileModal from "../user-profile-modal/UserProfileModal";
import MentionPostsModal from "../mention-posts-modal/mentionPostsModal";
import { TopPerformancePostsSkeleton, TopPerformancePostsSkeleton_6 } from "../skeleton-loader-cards/SkeletonCards";
class TopPerformancePosts extends Component {
  state = {
    get_top_post_performance: null,
    isLoading: true,
    show: false,
    total: 0,
    isOpen: false,
    userData: null,
    isPost: false,
    postData: null,
    windowWidth: window.innerWidth,
    windowHeight: window.innerHeight,
  }
  componentDidMount() {
    const { get_top_post_performance } = this.props;
    if (get_top_post_performance && get_top_post_performance.data) {
      this.setState({ get_top_post_performance: get_top_post_performance.data, total: get_top_post_performance.total }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
    }
    window.addEventListener("resize", () => {
      this.setState({ windowWidth: window.innerWidth, windowHeight: window.innerHeight })
    });
  }
  static getDerivedStateFromProps(props, state) {
    if (props.get_top_post_performance !== state.get_top_post_performance) {
      return {
        get_top_post_performance: props.get_top_post_performance.data,
        total: props.get_top_post_performance.total
      };
    }
    return null;
  }
  handleShow = event => {
    this.setState({ show: true });
  }
  handleClose = event => {
    this.setState({ show: false })
  }
  IsOpenUserProfile = (data) => event => this.setState({ isOpen: true, userData: data, show: false });
  IsCloseUserProfile = event => this.setState({ isOpen: false, userData: null });

  isPostOpen = (data) => event => this.setState({ isPost: true, postData: data, show: false });
  isPostClose = event => this.setState({ isPost: false, postData: null });





  render() {
    const { className, analyticsStatus, pageRequest } = this.props;
    const { isLoading, get_top_post_performance, total, userData } = this.state;
    return (
      <div className={`card card-custom ${className}`}>
        {pageRequest.loader ? this.state.windowWidth > 1600 ? <TopPerformancePostsSkeleton /> : <TopPerformancePostsSkeleton_6
        /> : <>
          <div className="card-header min-h-auto py-3">
            <div className="card-title justify-content-between w-100">
              <h5 className="card-label font-weight-bolder">Top Performing Posts</h5>
              {((total && parseInt(total) > 5) || analyticsStatus.trialAccessAPI) ? <button type="button" className="btn p-0 see_more_btn text-hover-primary" onClick={this.handleShow}>See More</button> : null}
            </div>
          </div>
          <div className="card-body">
            <div className="top_posts_row row">
          

              {pageRequest.loader ? <DataLoading /> : get_top_post_performance && get_top_post_performance.length > 0 ? get_top_post_performance.map((topItem, topIndex) => { if (this.state.windowWidth > 1600 ? topIndex < 5 : topIndex < 6) { return <TopPost data={topItem} key={uuidv4()} IsOpenUserProfile={this.IsOpenUserProfile} isPostOpen={this.isPostOpen} trialAccessAPI={analyticsStatus.trialAccessAPI} style={null} /> } }) : <DataNotFound />}

            </div>
          </div>

          {/* <SeeMoreModal size={'xxl'} showHide={this.state.show} handleClose={this.handleClose} title={'Top Performing Posts'}>
          <MoreTopPerformancePosts {...this.props} IsOpenUserProfile={this.IsOpenUserProfile} isPostOpen={this.isPostOpen} />
        </SeeMoreModal> */}
          {this.state.isOpen ? <UserProfileModal size={'lg'} showHide={this.state.isOpen} handleClose={this.IsCloseUserProfile} title={''} userData={userData} trialAccessAPI={analyticsStatus.trialAccessAPI} /> : null}
          {this.state.isPost ? <MentionPostsModal title={`Top Performing Posts`} size={'lg'} show={this.state.isPost} onHide={this.isPostClose} userData={this.state.postData} trialAccessAPI={analyticsStatus.trialAccessAPI} /> : null}
          {this.state.show ? <MoreTopPerformancePosts size={'lg'} onShow={this.state.show} onHide={this.handleClose} {...this.props} IsOpenUserProfile={this.IsOpenUserProfile} isPostOpen={this.isPostOpen} trialAccessAPI={analyticsStatus.trialAccessAPI} /> : null}
        </>}
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    get_top_post_performance: state.topPostPerformance.get_top_post_performance,
    pageRequest: state.topPostPerformance
  }
}
export default connect(mapStateToProps)(TopPerformancePosts);