/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { Component, memo } from "react";
import { connect } from "react-redux";
import ReactHtmlParser from 'react-html-parser';
import ReactMultiSelectCheckboxes from 'react-multiselect-checkboxes';
import { updateSelectedFeed } from '../../../../actions/analytics'
import { feedsNetworksPrefix, networkIcon } from '../../../../utils/feedsNetworkPrefix'


class FeedsList extends Component {
  state = { feed: [], wallID: null, updateFeedList: [], refreshing: true }
  componentDidMount() {
    const { analyticwall, analyticsStatus } = this.props
    let updateFeedList = []; let wallID = 0;
    if (analyticwall.selectedWallFeed && Object.keys(analyticwall.selectedWallFeed).length > 0) {
      if (analyticwall.selectedWallFeed && analyticwall.selectedWallFeed.feeds.length > 0) {
        wallID = analyticwall.selectedWallFeed.wall.id;
        analyticwall.selectedWallFeed.feeds.map((item) => {
          updateFeedList.push({ value: item.id, label: ReactHtmlParser(`${item.Network.icon ? networkIcon(item.Network.icon) : ""}  ${feedsNetworksPrefix(item.filterId)}  ${item.name}`) })
        })

      }

    }
    this.setState({ updateFeedList, wallID, feed: this.state.wallID != wallID ? updateFeedList : this.state.feed }, () => this.props.updateSelectedFeed(this.state.feed, analyticsStatus.trialAccessAPI))
  }
  changeFeedsFilters = (event) => {
    const { analyticsStatus } = this.props;
    this.setState({ feed: event }, () => this.props.updateSelectedFeed(this.state.feed, analyticsStatus.trialAccessAPI))

  }

  componentWillReceiveProps(nextProps) {
    const { analyticwall, wallRefresh } = nextProps
    const { refreshing } = this.state;
    let updateFeedList = []; let wallID = 0;
    if (analyticwall.selectedWallFeed && Object.keys(analyticwall.selectedWallFeed).length > 0) {
      if (analyticwall.selectedWallFeed && analyticwall.selectedWallFeed.feeds.length > 0) {
        wallID = analyticwall.selectedWallFeed.wall.id;
        analyticwall.selectedWallFeed.feeds.map((item) => {
          updateFeedList.push({ value: item.id, label: ReactHtmlParser(`${item.Network.icon ? networkIcon(item.Network.icon) : ""}  ${feedsNetworksPrefix(item.filterId)}  ${item.name}`) })
        })

      }

    }
    if (updateFeedList != this.state.updateFeedList && wallRefresh && wallRefresh.refreshing && refreshing) {
      this.setState({ updateFeedList, wallID, feed: updateFeedList, refreshing: false })
    }
    else {
      this.setState({ updateFeedList, wallID, feed: this.state.wallID != wallID ? updateFeedList : this.state.feed }, () => setTimeout(() => this.setState({ refreshing: true }), 2000))
    }
  }

  render() {
    const { analyticwall, analyticsStatus } = this.props
    const { updateFeedList, feed, wallID } = this.state;

    return <div className="w-auto react-search-feed">

      {/* <Select key={wallID} className="feeds-filter"
      placeholder="Please select feeds"
      isMulti
      isSearchable={false}
      value={feed}
      onChange={this.changeFeedsFilters}
      options={updateFeedList}
    /> */}
      <ReactMultiSelectCheckboxes
        classNamePrefix={`check-feed`}
        value={feed}
        onChange={this.changeFeedsFilters}
        options={updateFeedList}
      />
      {/* <Multiselect
        showCheckbox={true}
        showArrow={true}
        options={updateFeedList} // Options to display in the dropdown
        selectedValues={feed} // Preselected value to persist in dropdown
        onSelect={this.changeFeedsFilters} // Function will trigger on select event
        // Function will trigger on remove event
        displayValue="label" // Property name to display in the dropdown options
      /> */}
    </div>
  }
}


const mapStateToProps = (state) => {
  return {
    analyticwall: state.analyticwall,
    analyticsStatus: state.analyticsStatus,
    wallRefresh: state.wallRefresh
  }
}
const mapDispatchToProps = dispatch => {
  return {
    updateSelectedFeed: (data, isTrial) => dispatch(updateSelectedFeed(data, isTrial)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(memo(FeedsList));