import React, { useState } from "react";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import './enableAnalytics.css'
import { enableAnalytics } from '../actions/analytics'

const EnableAnalytics = (props) => {
    const dispatch = useDispatch()
    const [visible, setVisible] = useState(false)
    setTimeout(() => setVisible(true), 3100)
    const { analyticsWallID } = props.analyticsStatus;
    const onClickEnableAnalytics = (analyticsWallID) => event => dispatch(enableAnalytics(analyticsWallID))


    return visible ? <div className="select_wall my-0 rounded-0 border-0 card enable_analytics_background_transparent">
        <div className="p-0 card-body">
            <div className="row">
                <div className="enable_analytics_loader">
                    <div className="enable_analytics"><button onClick={onClickEnableAnalytics(analyticsWallID)} type="button" className="btn btn-primary btn-min-w btn btn-primary">Enable Analytics</button></div>
                </div>

            </div>

        </div>

    </div> : null
}
export default EnableAnalytics