import {
    API_GET_WALL_RECORD_WORD_CLOUD_LOADER,
    GET_WALL_RECORD_WORD_CLOUD
} from './keys'
const initialState = {
    record_word_cloud: [],
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_WORD_CLOUD: {
            return { ...state, record_word_cloud: action.payload }
        }
        case API_GET_WALL_RECORD_WORD_CLOUD_LOADER: {
            return { ...state, loader: action.payload}
        }
        default:
            return { ...state }
    }

}