import {
    API_GET_WALL_RECORD_POST_CLICK_BY_PLATFORMS_LOADER,
    GET_WALL_RECORD_POST_CLICK_BY_PLATFORMS
} from './keys'
const initialState = {
    record_post_click_by_platforms: [],
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_POST_CLICK_BY_PLATFORMS: {
            return { ...state, record_post_click_by_platforms: action.payload }
        }
        case API_GET_WALL_RECORD_POST_CLICK_BY_PLATFORMS_LOADER: {
            return { ...state, loader: action.payload}
        }
        default:
            return { ...state }
    }

}