import React, { Component } from "react";
import ErrorFound from '../errorFallback'
import Counters from '../counters/Counters'
import WordCloud from '../word-cloud/WordCloud'
import PostOverview from '../post-overview/PostOverview'
import MentionSharedPosts from '../mention-shared-posts/MentionSharedPosts'
import TopPerformancePosts from '../top-performance-posts/TopPerformancePosts'
import Contributors from '../contributors/Contributors'
import ActiveUsers from '../active-users/ActiveUsers'
import PostPlatforms from '../post-platforms/PostPlatforms'
import Overview from '../overview/Overview'
import PostGraph from '../posts-graph/PostGraph'
import Sentiments from '../sentiments/Sentiments'

class WallDashboard extends Component {

    render() {
        return (
            <div className="row">
                <div className="col-lg-12 col-xxl-12">
                    <ErrorFound><Counters {...this.props} className="card-stretch gutter-b" />  </ErrorFound>
                </div>
                <div className="col-lg-12 col-xxl-12">
                    <ErrorFound><Overview {...this.props} className="card-stretch gutter-b Overview" /></ErrorFound>
                </div>
                <div className="col-lg-6 col-xxxl-4">
                    <ErrorFound><PostGraph {...this.props} className="card-stretch gutter-b" /></ErrorFound>
                </div>
                <div className="col-lg-6 col-xxxl-4">
                    <ErrorFound><Sentiments {...this.props} className="card-stretch gutter-b" /></ErrorFound>
                </div>
                <div className="col-lg-6 col-xxxl-4">
                    <ErrorFound><WordCloud {...this.props} className="card-stretch gutter-b" /></ErrorFound>
                </div>
                <div className="col-lg-6 col-xxxl-4">
                    <ErrorFound><Contributors {...this.props} className="card-stretch gutter-b Contributors" /></ErrorFound>
                </div>
                <div className="col-lg-6 col-xxxl-4">
                    <ErrorFound><ActiveUsers {...this.props} className="card-stretch gutter-b ActiveUsers" /></ErrorFound>
                </div>
                <div className="col-lg-6 col-xxxl-4">
                    <ErrorFound><PostOverview {...this.props} className="card-stretch gutter-b" /></ErrorFound>
                </div>
                <div className="col-lg-6 col-xxl-6">
                    <ErrorFound><MentionSharedPosts {...this.props} className="card-stretch gutter-b MentionSharedPosts" /></ErrorFound>
                </div>
                <div className="col-lg-6 col-xxl-6">
                    <ErrorFound><PostPlatforms {...this.props} className="card-stretch gutter-b" /></ErrorFound>
                </div>
                <div className="col-lg-12 col-xxl-12">
                    <ErrorFound><TopPerformancePosts {...this.props}  className="card-stretch gutter-b TopPerformancePosts" /></ErrorFound>
                </div>
            </div>
        );
    }
}
export default WallDashboard;