import { LOGIN_SESSION_CHECK, LOGIN_USER_DATA } from './keys'
import userTableMock from '../app/modules/Auth/__mocks__/userTableMock'
const initialState = {
    user: userTableMock[1],
    isAuthorized: localStorage.getItem("token") ? true : false,
    userData: {
        email: null,
        updateTimeMessage: null,
        upgradeMessage: null,
        username: null,
        userPlanDetails: null
    }

}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case LOGIN_SESSION_CHECK: {
            return { ...state, isAuthorized: (action.payload) ? true : false }
        }
        case LOGIN_USER_DATA: {
            return { ...state, userData: action.payload }
        }
        default:
            return { ...state }
    }

}