import React, { Component } from "react";
import { connect } from "react-redux";
import { v4 as uuidv4 } from 'uuid';
import ChartPostOverview from './lineChart'
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { PostOverviewSkeleton } from "../skeleton-loader-cards/SkeletonCards";

class PostOverview extends Component {
  state = {
    record_post_overview: null,
    isLoading: true
  }
  componentDidMount() {
    const { record_post_overview } = this.props;
    if (record_post_overview) {
      this.setState({ record_post_overview: record_post_overview }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
    }
  }
  static getDerivedStateFromProps(props, state) {
    if (props.record_post_overview !== state.record_post_overview) {
      return {
        record_post_overview: props.record_post_overview
      };
    }
    return null;
  }

  render() {
    const { isLoading, record_post_overview } = this.state;
    const { pageRequest } = this.props
    const color = ['#FFB94B', '#56BEFF', '#FF7E89', '#9B82FF', '#FFB94B', '#56BEFF', '#FF7E89', '#9B82FF'];
    return (
      <div className={`card card-custom ${this.props.className}`}>
        {pageRequest.loader ? <PostOverviewSkeleton /> : <>
          <div className="card-header min-h-auto py-3">
            <div className="card-title">
              <h5 className="card-label font-weight-bolder">Post Overview</h5>

            </div>
          </div>
          <div className="card-body p-0">

            {
              pageRequest.loader ? <DataLoading /> : (record_post_overview && record_post_overview.length > 0) ? record_post_overview.map((item, index) => <ChartPostOverview color={color} item={item} index={index} key={uuidv4()} />) : <DataNotFound />
            }
          </div>
        </>}
      </div>
    );
  }
}



const mapStateToProps = (state) => {
  return {
    record_post_overview: state.wallRecordPostOverview.record_post_overview,
    pageRequest: state.wallRecordPostOverview
  }
}
export default connect(mapStateToProps)(PostOverview);