import {
    GET_POST_CLICK_PLATFORMS
} from './keys'
const initialState = {
    get_post_click_platforms: []
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_POST_CLICK_PLATFORMS: {
            return { ...state, post_click_by_platforms: action.payload }
        }
        default:
            return { ...state }
    }

}