const wallIDFetch = (wallID, data) => {
    if (data && data.length > 0) {
        if (wallID) {
            const filterData = data.filter((item) => item.wall.id == wallID)
            if (filterData && filterData.length > 0) return filterData[0]
            else return data[0];
        }
        else return data[0];
    }
}

export const userInfluentialContributors = (username, limit, offset) => {
    const { selectedWallFeed, dateFilter, selectedFeed } = store.getState().analyticwall;
    const { trialAccessAPI } = store.getState().analyticsStatus;
    let wallData = null;
    if (selectedWallFeed && selectedWallFeed.wall) {
        wallData = selectedWallFeed.wall.id;
    }
    let startDate = dateFilter.startDate;
    let endDate = dateFilter.endDate;
    let feedIDs = [];
    if (selectedFeed && selectedFeed.length > 0) {
        selectedFeed.map((fItem) => {
            feedIDs.push(fItem.value);
        })
    }
    const data = { "wall_id": wallData, "startdaterange": moment(startDate).format("YYYY-MM-DD HH:mm:ss"), "enddaterange": moment(endDate).format("YYYY-MM-DD HH:mm:ss"), "feedlist": feedIDs.join() ? feedIDs.join() : '9999', "username": username, "offset": offset, "limit": limit , apiCall:trialAccessAPI ? 1 : 0 }
    return new HttpClient().post(`${API_GET_WALL_POST_BY_USERNAME}`, data)
};

export const getWallFeeds = (userID) => {
    return (dispatch) => {
        new HttpClient()
            .get(`${GET_WALL_FEED}/${userID}`)
            .then((response) => {
                const getWallID = getAnalyticWallID();
                dispatch({
                    type: GET_USER_FEEDS,
                    payload: { data: response.data, selectedData: wallIDFetch(getWallID, response.data) },
                });
                dispatch({
                    type: UPDATE_WALL_AND_FEED,
                    payload: wallIDFetch(getWallID, response.data),
                });

            })
            .catch((error) => {
                console.error("LAYOUT ISSUE", error)
                dispatch({ type: PAGE_NOT_FOUND, payload: true });
            });
    }
};
export const currentUpdateWallAndFeed = (data, isTrial) => {
    return (dispatch) => {
        if (!isTrial) {
            new HttpClient()
                .get(`${GET_ANALYTICS_STATUS}/${data.wall.id}`)
                .then((response) => {
                    const { responseCode, responseData } = response.data;
                    if (responseCode == 200) {
                        dispatch({ type: ANALYTICS_STATUS, payload: responseData });

                        const { analytics, webAnalytic, id } = responseData;
                        const trialAccessAPI = analytics && webAnalytic ? false : true;
                        currentNowLoadData(dispatch, data, trialAccessAPI)
                    }
                }).catch((error) => {
                    console.error("LAYOUT ISSUE", error)
                    dispatch({ type: PAGE_NOT_FOUND, payload: true });
                });
        }
        else {
            currentNowLoadData(dispatch, data, isTrial)
        }
    }
}

const currentNowLoadData = (dispatch, data, isNewTrial) => {
    dispatch({
        type: UPDATE_WALL_AND_FEED,
        payload: data,
    });
    const { selectedWallFeed, dateFilter, selectedFeed } = store.getState().analyticwall;
    let wallData = null;
    if (selectedWallFeed && selectedWallFeed.wall) {
        wallData = selectedWallFeed.wall.id;
    }
    let startDate = dateFilter.startDate;
    let endDate = dateFilter.endDate;
    let feedIDs = [];
    if (selectedFeed && selectedFeed.length > 0) {
        selectedFeed.map((fItem) => {
            feedIDs.push(fItem.value);
        })
    }
    if (isNewTrial) {
        trialAnalyticsDataGetFromAPI(dispatch, moment(startDate).format("YYYY-MM-DD HH:mm:ss"), moment(endDate).format("YYYY-MM-DD HH:mm:ss"))
    }
    else fetchData(dispatch, wallData, moment(startDate).format("YYYY-MM-DD HH:mm:ss"), moment(endDate).format("YYYY-MM-DD HH:mm:ss"), feedIDs.join(), 6)
}
