export const TAGGBOX_USER_LOGIN = () => {
    var wallID = getAnalyticWallID();
    var ownerID = window.ownerID;
    const TEST = `${HOST_URL_TAGGSHOP}analytic/`
    if (window.location.pathname.includes(TEST)) {
        wallID = window.location.pathname.split(TEST)[1]
    }
    else {
        wallID = getAnalyticWallID();

    }

    return (dispatch) => {


        dispatch({
            type: WALL_ID,
            payload: { wallID, ownerID },
        });
        // dispatch({
        //     type: LOGIN_USER_DATA,
        //     payload: {
        //         "email": "fixed@mailinator.com",
        //         "username": "fixed arnodya",
        //         "upgradeMessage": "",
        //         "updateTimeMessage": "",
        //         "userPlanDetails": {
        //             "planIds": [
        //                 "45",
        //                 "38",
        //                 "31",
        //                 "29"
        //             ],
        //             "commercePlan": "EC-Startup",
        //             "ugcPlan": "RM-Starter",
        //             "displayPlan": "DS-Launch",
        //             "widgetPlan": "WB-Professional(Pro)"
        //         }
        //     },
        // });
        axios.get(`${GET_STUDIO_USER_LOGIN_SESSION}`, { withCredentials: true })
            .then((response) => {
                const { response_code, user, isAdmin } = response.data;
                if (response_code == 200) {
                    new HttpClient()
                        .post(`${GET_STUDIO_USER_CHECK_SESSION}`, { user: user })
                        .then((tokenResponse) => {
                            localStorage.setItem('token', tokenResponse.data.token)
                            dispatch({
                                type: LOGIN_SESSION_CHECK,
                                payload: tokenResponse.data.token,
                            });
                            dispatch({
                                type: LOGIN_USER_DATA,
                                payload: {
                                    ...tokenResponse.data.user_data, role: isAdmin
                                }
                            });

                        })
                        .catch((error) => {
                            console.error("ERROR", error)

                        });
                }

            })
            .catch((error) => {
                console.error("ERROR", error)
            });
    }
}

