import {
    API_GET_WALL_TOP_POST_PERFORMANCE_LOADER,
    GET_TOP_POST_PERFORMANCE
} from './keys'
const initialState = {
    get_top_post_performance: { data: [], total: 0 },
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_TOP_POST_PERFORMANCE: {
            return { ...state, get_top_post_performance: action.payload }
        }
        case API_GET_WALL_TOP_POST_PERFORMANCE_LOADER: {
            return { ...state, loader: action.payload }
        }
        default:
            return { ...state }
    }

}