import React, { useMemo, useRef, createRef } from "react";
import objectPath from "object-path";
import { useHistory } from "react-router-dom";
// LayoutContext
import { useScreenshot } from "use-react-screenshot";
import ReactToPdf from 'react-to-pdf'
import { exportComponentAsJPEG, exportComponentAsPDF, exportComponentAsPNG } from 'react-component-export-image';
import { useHtmlClassService } from "../_core/MetronicLayout";
// Import Layout components
import { Header } from "./header/Header";
import { HeaderMobile } from "./header-mobile/HeaderMobile";
import { Aside } from "./aside/Aside";
import { LayoutInit } from "./LayoutInit";
import { SubHeader } from "./subheader/SubHeader";
import { QuickPanel } from "./extras/offcanvas/QuickPanel";
import QuickUser from "./extras/offcanvas/QuickUser";
import { ScrollTop } from "./extras/ScrollTop";


export function Layout(props) {
    const history = useHistory();
    const ref = createRef();
    const { children, analyticsStatus } = props;
    const uiService = useHtmlClassService();
    const componentRef = useRef();
    const [image, takeScreenShot] = useScreenshot();
    // Layout settings (cssClasses/cssAttributes)
    const layoutProps = useMemo(() => {
        return {
            layoutConfig: uiService.config,
            selfLayout: objectPath.get(uiService.config, "self.layout"),
            asideDisplay: objectPath.get(uiService.config, "aside.self.display"),
            subheaderDisplay: objectPath.get(uiService.config, "subheader.display"),
            desktopHeaderDisplay: objectPath.get(
                uiService.config,
                "header.self.fixed.desktop"
            ),
            contentCssClasses: uiService.getClasses("content", true),
            contentContainerClasses: uiService.getClasses("content_container", true),
            contentExtended: objectPath.get(uiService.config, "content.extended")
        };
    }, [uiService]);

    const getImage = () => takeScreenShot(ref.current);
    //const getImage = () => exportComponentAsPDF(ref, { orientation: 'l' });
    return layoutProps.selfLayout !== "blank" ? (
        <>
{/*            
            <HeaderMobile />
            <div className="d-flex flex-column flex-root">
 
                <div className="d-flex flex-row flex-column-fluid page">
                    {layoutProps.asideDisplay && (<Aside />)}
 
                    <div className={`d-flex flex-column flex-row-fluid wrapper ${(analyticsStatus && analyticsStatus.analytics) ? `` : `p-0`}`} id="kt_wrapper" >
                        <Header />
                        */}
                        <div

                            id="kt_content"
                            className={`content ${layoutProps.contentCssClasses} d-flex flex-column flex-column-fluid`}
                        >
                            {layoutProps.subheaderDisplay && (analyticsStatus && analyticsStatus.analytics) && <SubHeader updatedRef={ref} />}
                            {/*begin::Entry*/}
                            {!layoutProps.contentExtended && (
                                <div className="d-flex flex-column-fluid">
                                    {/*begin::Container*/}
                                    {/* <div className={layoutProps.contentContainerClasses}> */}
                                    <div className="container-fluid" ref={ref}>
                                        {(analyticsStatus && analyticsStatus.analytics && analyticsStatus.trialAccessAPI) ? <div className="row">
                                            <div className="col-lg-12 col-xxl-12">
                                                <div className="alert custom-alert">
                                                    <span>This Is The Demo Data. Upgrade Your Plan To Preview The Accurate Data For Your Account.</span>
                                                    <a className="free-trial-upgrade" href="https://taggbox.com/price/commerce-price/" target="_blank">
                                                        Upgrade Now
                                                    </a>
                                                </div>

                                            </div>
                                        </div> : null}

                                        {
                                            children
                                        }

                                    </div>
                                    {/*end::Container*/}
                                </div>
                            )}

                            {layoutProps.contentExtended && { children }}
                            {/*end::Entry*/}
                        </div>
                        {/*end::Content*/}
                    {/* </div> 
                </div> 
            </div> */}
            {/* <QuickUser history={history} />
            <QuickPanel />
            <ScrollTop /> */}
            {/*end::Main*/}
            {/* <LayoutInit /> */}
        </>
    ) : (
            // BLANK LAYOUT
            <div className="d-flex flex-column flex-root">{children}</div>
        );
}
