import React, { Component } from "react";
import { connect } from "react-redux";
import { v4 as uuidv4 } from 'uuid';
import moment from 'moment'
import SVG from "react-inlinesvg";
import { Button, Modal } from "react-bootstrap";
import { toAbsoluteUrl } from "../../_helpers";
import { kFormatter } from '../../../utils'
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { getwallRecordPostByHighestSharedForPopUp } from '../../../actions/analytics'
import { paginate } from '../../../utils/paginate'
import { MentionAllPostModalSkeletonCard } from "../skeleton-loader-cards/PopupSkeletonCards";



class AllPostsModal extends Component {
    state = {
        record_post_by_highest_shared: null,
        isLoading: true,
        total: 0,
        limit: 10,
        offset: 0,
        paginateData: {},
        currentPage: 1,
        maxPage: 10
    }

    componentDidMount() {
        const { record_post_by_highest_shared, analyticwall } = this.props;
        const { limit, currentPage, maxPage } = this.state;
        //getwallRecordMostActiveUsersForPopUp()
        if (record_post_by_highest_shared && record_post_by_highest_shared.length > 0) {
            this.setState({ total: record_post_by_highest_shared[0].total, paginateData: paginate(record_post_by_highest_shared[0].total, currentPage, limit, maxPage) }, () => this.onLoadFilterData())
        }
    }
    onLoadFilterData = () => {
        const { analyticwall } = this.props;
        const { limit, offset } = this.state;
        const { selectedWallFeed, dateFilter, selectedFeed } = analyticwall;

        let wallData = null;
        if (selectedWallFeed && selectedWallFeed.wall) {
            wallData = selectedWallFeed.wall.id;
        }
        let startDate = dateFilter.startDate;
        let endDate = dateFilter.endDate;

        let feedIDs = [];
        if (selectedFeed && selectedFeed.length > 0) {
            selectedFeed.map((fItem) => {
                feedIDs.push(fItem.value);
            })
        }
        this.setState({ isLoading: true })
        getwallRecordPostByHighestSharedForPopUp(wallData, moment(startDate).format("YYYY-MM-DD HH:mm:ss"), moment(endDate).format("YYYY-MM-DD HH:mm:ss"), feedIDs.join(), limit, offset).then((response) => {
            const { responseData } = response.data;
            this.setState({ record_post_by_highest_shared: responseData, isLoading: false })
        })
            .catch((error) => {
                console.error("LAYOUT ISSUE", error)
            });

    }
    onUpdateDataLimit = event => {
        const { total, currentPage, limit, maxPage } = this.state;
        const pageCount = parseInt(currentPage) == 1 ? 0 : parseInt(currentPage);
        const offset = parseInt(event.target.value) * pageCount;
        this.setState({ limit: event.target.value, paginateData: paginate(total, currentPage, event.target.value, maxPage), offset: offset }, () => this.onLoadFilterData())
    }
    onUpdatePage = (page) => event => {
        const { total, limit, maxPage } = this.state;
        const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
        const offset = parseInt(limit) * pageCount;
        this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())

    }
    onPrevNavigateClick = event => {
        const { total, limit, maxPage, paginateData } = this.state;
        let page = 1;
        if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
            page = (paginateData.currentPage - 1)
        }
        if (page && page > 0) {
            const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
            const offset = parseInt(limit) * pageCount;
            this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())
        }
    }
    onNextNavigateClick = event => {
        const { total, limit, maxPage, paginateData } = this.state;
        let page = 1;
        if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
            page = (paginateData.currentPage + 1)
        }
        if (page && paginateData && Object.keys(paginateData).length > 0 && paginateData.totalPages >= page) {
            const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
            const offset = parseInt(limit) * pageCount;
            this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())
        }
    }

    render() {
        const { onShow, onHide, size } = this.props;
        const { isLoading, record_post_by_highest_shared, total, limit, paginateData, currentPage } = this.state;


        return (
            <Modal show={onShow}
                onHide={onHide}
                backdrop="static"
                keyboard={false}
                aria-labelledby="contained-modal-title-vcenter"
                centered
                size={size}>
                <Modal.Header closeButton>
                    <h5 className="m-0">All Posts</h5>
                </Modal.Header>
                <Modal.Body className="p-md-7 p-3">
                    {isLoading ? <div className="h-100"><MentionAllPostModalSkeletonCard /></div> :

                        <div className="user_details_section">
                            <div className="users_list">
                                {record_post_by_highest_shared[0].sharedPosts && record_post_by_highest_shared[0].sharedPosts.length > 0 ? record_post_by_highest_shared[0].sharedPosts.map((mItem, mIndex) => <div className="card px-sm-5 px-2 py-sm-3 py-2 mb-3" key={uuidv4()}>
                                    <div className="row">

                                        {mItem.type != 1 ? <div className="col-lg-4 user_profile_items">
                                            <img src={mItem.image ? mItem.image : 'https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg'} className="img-fluid w-100 bg-light" onError={event => event.target.src = `https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg`}/>
                                        </div> : null}
                                        <div className={`col-lg-${mItem.type == 1 ? 12 : 8} user_profile_items_info pl-lg-5 mt-lg-0 mt-5`}>
                                            <h6 className="m-0 d-flex justify-content-between user_profile_items_title">
                                                <strong className="overflow-hidden text-nowrap text-ellipsis">{mItem.author.name}</strong>
                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                    `/media/svg/social-icons/${mItem.network.icon}.svg`
                                                )}
                                                ></SVG>
                                            </h6>
                                            <p><span>@{mItem.author.username}</span></p>
                                            
                                            <div className="user_profile_others mt-lg-0 mt-2">
                                            <div className="row">
                                                <div className="col-sm-6">
                                                    <div className="user_profile_disc">
                                                        <p>
                                                            {mItem.content}
                                                        </p>
                                                    </div>
                                                </div>
                                                <div className="col-sm-6">
                                                    <div className="user_profile_data ml-sm-7">
                                                    <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                                                        <span className="w-50 d-inline-block mr-1 text-nowrap">
                                                            <span className="svg-icon svg-icon-sm svg-icon-dark-75">

                                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                    `/media/svg/post-icon/impressions.svg`
                                                                )}
                                                                ></SVG>

                                                            </span> Impressions
                                                    </span>
                                                        <strong className="d-inline-block pl-sm-5">{kFormatter(parseInt(mItem.Impressions))}</strong>
                                                    </p>
                                                    <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                                                        <span className="w-50 d-inline-block mr-1 text-nowrap">
                                                            <span className="svg-icon svg-icon-sm svg-icon-dark-75">

                                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                    `/media/svg/post-icon/post_click.svg`
                                                                )}
                                                                ></SVG>

                                                            </span> Post Clicks
                                                    </span>
                                                        <strong className="d-inline-block pl-sm-5">{kFormatter(parseInt(mItem.postClick))}</strong>
                                                    </p>
                                                    <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                                                        <span className="w-50 d-inline-block mr-1 text-nowrap">
                                                            <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                    `/media/svg/post-icon/CTA.svg`
                                                                )}
                                                                ></SVG>

                                                            </span> Clicked CTA
                                                    </span>
                                                        <strong className="d-inline-block pl-sm-5">{kFormatter(parseInt(mItem.postCTA))}</strong>
                                                    </p>
                                                    <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                                                        <span className="w-50 d-inline-block mr-1 text-nowrap">
                                                            <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                                                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                    `/media/svg/post-icon/total_reach.svg`
                                                                )}
                                                                ></SVG>
                                                            </span> Total Reach
                                                    </span>
                                                        <strong className="d-inline-block pl-sm-5">{kFormatter(parseInt(mItem.potentialReach))}</strong>
                                                    </p>
                                                </div>
                                                </div>
                                            </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>) : <DataNotFound />}

                            </div>
                        </div>
                    }

                </Modal.Body>
                <Modal.Footer>
                    <>
                        {!isLoading && total > 0 && paginateData && Object.keys(paginateData).length > 0 ? <div className="w-100 d-flex justify-content-lg-between justify-content-center align-items-center flex-wrap px-lg-6 py-lg-4 px-sm-6 py-sm-4 px-0 pt-3">
                            <div className="d-flex flex-wrap py-2 mr-3 justify-content-sm-normal justify-content-center">

                                <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onPrevNavigateClick}>
                                    <i className="ki ki-bold-arrow-back icon-xs"></i>
                                </a>

                                {
                                    (paginateData.pages && paginateData.pages.length > 0) ? paginateData.pages.map((item, pagesIndex) => {
                                        return (parseInt(pagesIndex) < 5) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : (parseInt(paginateData.pages.length) - 2 == pagesIndex) ? <a className="btn btn-icon btn-sm border-0 btn-hover-primary mr-2 my-1">...</a> : (parseInt(paginateData.pages.length) - 1 == pagesIndex) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : null

                                    }) : null
                                }
                                <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onNextNavigateClick}>
                                    <i className="ki ki-bold-arrow-next icon-xs"></i>
                                </a>
                            </div>
                            <div className="d-flex align-items-center py-1 shwoing-filter">
                                <select
                                    className="form-control form-control-sm font-weight-bold mr-4 border-0 bg-light false"
                                    style={{ width: 75 }}
                                    onChange={this.onUpdateDataLimit}
                                    value={limit}
                                >

                                    <option className="" value={10}>10</option>
                                    <option className="" value={20}>20</option>
                                    <option className="" value={50}>50</option>
                                </select>
                                <span className="react-bootstrap-table-pagination-total">
                                    &nbsp;Showing rows {paginateData.currentPage} to&nbsp;{paginateData.totalPages} of&nbsp;{total}
                                </span>
                            </div>
                        </div> : null}

                    </>

                </Modal.Footer>
            </Modal>
        );
    }
}


const mapStateToProps = (state) => {
    return {
        analyticwall: state.analyticwall
    }
}
export default connect(mapStateToProps)(AllPostsModal);