import React, { Suspense, lazy } from "react";
import { Redirect, Switch, Route, useHistory } from "react-router-dom";
import { LayoutSplashScreen, ContentRoute } from "../_analytics/layout";
import { DashboardPage } from "./pages/DashboardPage";
const BasePage = (props) => {
    const history = useHistory();
    const intialRoute = `/taggshop/analytic/${props.wall.wallID}`
    return (
        <Suspense>
            {
                <Switch>
                    {
                        <Redirect from={intialRoute} to={`${intialRoute}`} />
                    }
                    <ContentRoute path="/analytic/:wallID" component={DashboardPage} history={history} />
                    {/* <ContentRoute path={`${intialRoute}/analytic/:wallURL`} component={DashboardPage} /> */}
                </Switch >

            }

        </Suspense >
    );
}
export default BasePage