import {
    API_GET_WALL_RECORD_OVERVIEW_LOADER,
    GET_WALL_RECORD_OVERVIEW
} from './keys'
const initialState = {
    record_overview: [],
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_OVERVIEW: {
            return { ...state, record_overview: action.payload }
        }
        case API_GET_WALL_RECORD_OVERVIEW_LOADER: {
            return {...state, loader: action.payload}
        }
        default:
            return { ...state }
    }

}