import {
    API_GET_WALL_RECORD_MOST_ACTIVE_USERS_LOADER,
    GET_WALL_RECORD_MOST_ACTIVE_USERS
} from './keys'
const initialState = {
    record_most_active_users: { data: [], total: 0 },
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_MOST_ACTIVE_USERS: {
            return { ...state, record_most_active_users: action.payload }
        }
        case API_GET_WALL_RECORD_MOST_ACTIVE_USERS_LOADER: {
            return {...state, loader: action.payload}
            }
        default:
            return { ...state }
    }

}