import { ANALYTICS_STATUS, ANALYTICS_TRIAL_API_ACCESS, UPDATE_ANALYTICS_STATUS } from './keys'
const initialState = {
    analytics: false,
    webAnalytic: false,
    trialAccessAPI: false,
    analyticsWallID: 0,
    isEnableButton: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case ANALYTICS_STATUS: {
            let { analytics, webAnalytic, id, pid, isDowngrade } = action.payload;
            let trialAccessAPI = analytics && webAnalytic ? false : true;

            var isEnableButtonVisible = false;
            if (pid && pid.length > 0) {
                pid.map((item) => {

                    if (["38", "39", "56"].includes(item) && !isEnableButtonVisible) {
                        isEnableButtonVisible = true;
                    }
                })
            }


            if (isDowngrade && isDowngrade != "" && Object.keys(isDowngrade).length > 0) {
                if ([38, 39, 56].includes(isDowngrade.planId) && !isEnableButtonVisible) {
                    isEnableButtonVisible = true;
                }
            }

            if (!isEnableButtonVisible) {
                trialAccessAPI = true;
                analytics = true;
            }
            console.log("isEnableButtonVisible action.payload 2", isEnableButtonVisible, action.payload)
            return { ...state, analytics, webAnalytic, trialAccessAPI, analyticsWallID: id, isEnableButton: isEnableButtonVisible }
        }
        case ANALYTICS_TRIAL_API_ACCESS: {
            return { ...state, trialAccessAPI: action.payload }
        }
        case UPDATE_ANALYTICS_STATUS: {
            return { ...state, analytics: action.payload }
        }
        default:
            return { ...state }
    }

}