import React, { Component } from "react";
import moment from 'moment';
import { connect } from "react-redux";
import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../_helpers";
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { v4 as uuidv4 } from 'uuid';
import SeeMoreModal from "../see-more-modal/SeeMoreModal";
import MoreMentionPost from './moreMentionPost';
import AllPostsModal from "../all-posts-modal/AllPostsModal";
import MentionPostsModal from "../mention-posts-modal/mentionPostsModal";
import UserProfileModal from "../user-profile-modal/UserProfileModal";
class MentionPosts extends Component {
    state = {
        record_post_by_mention: null,
        isLoading: true,
        show: false,
        total: 0,
        isOpen: false,
        userData: null,
        isUserOpen: false,
    }
    componentDidMount() {
        const { record_post_by_mention } = this.props;
        if (record_post_by_mention && record_post_by_mention.length > 0) {
            this.setState({ record_post_by_mention: record_post_by_mention, total: record_post_by_mention[0].total }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
        }
    }
    static getDerivedStateFromProps(props, state) {
        if (props.record_post_by_mention !== state.record_post_by_mention) {
            return {
                record_post_by_mention: props.record_post_by_mention,
                total: props.record_post_by_mention.length > 0 ? props.record_post_by_mention[0].total : 0,
                isLoading: props.record_post_by_mention.length > 0 ? false : true
            };
        }
        return null;
    }

    handleShow = event => {
        this.setState({ show: true });
    }
    handleClose = event => {
        this.setState({ show: false })
    }



    userOpen = (userData) => event => this.setState({ isOpen: true, userData, show: false });
    userClose = event => this.setState({ isOpen: false, userData: null });

    IsOpenUserProfile = (data) => event => this.setState({ isUserOpen: true, userData: data, show: false });
    IsCloseUserProfile = event => this.setState({ isUserOpen: false, userData: null });
    render() {
        const { className, analyticsStatus } = this.props;
        const { isLoading, record_post_by_mention, total } = this.state;

        return <>
            {/* <button type="button" className="btn p-0 see_more_btn text-hover-primary" onClick={this.openModal}>See More</button> */}
            {((total && parseInt(total) > 5) || analyticsStatus.trialAccessAPI) ? <button type="button" className="btn p-0 see_more_btn see_more_btn_tabs text-hover-primary" onClick={this.handleShow}>See More</button> : null}
            {
                isLoading ? <DataLoading /> : record_post_by_mention[0].mentionsPosts && record_post_by_mention[0].mentionsPosts.length > 0 ? record_post_by_mention[0].mentionsPosts.map((mItem, mIndex) => <div className="px-5 px-xxl-5 py-3 cursor-pointer border-bottom" key={uuidv4()}>
                    <div className="d-flex align-items-center">
                        <div className="symbol_item position-relative" onClick={this.userOpen(mItem)}>

                            {mItem.type != 1 ? <div className="symbol symbol-100 symbol-2by2 flex-shrink-0 mr-4">
                                <button type="button" className="border-0 p-0 bg-transparent" >
                                    <span className="symbol-label rounded-0 no-repeat-fit-background" style={{ backgroundImage: `url(${mItem.image}), url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span>
                                </button>
                            </div> : null}

                        </div>
                        <div className="d-flex flex-column flex-grow-1">
                            <div className="d-flex flex-grow-1 w-100 mb-2" onClick={this.IsOpenUserProfile(mItem)}>
                                <div className="symbol symbol-45 symbol-light mr-3 con_user_img cursor-pointer">
                                    <span className="no-repeat-fit-background" style={{ backgroundImage: `url(${mItem.author.image ? mItem.author.image.replace(/\s/g, '') : ""}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span>
                                    <i className="icon_on_img w-20 h-20" style={{ height: 20, right: `0px`, top: `-5px` }} >
                                        <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                            `/media/svg/social-icons/${mItem.network.icon}.svg`
                                        )}
                                        ></SVG>
                                    </i>
                                </div>
                                <div className="d-flex text-left flex-column conn_user_info w-100 position-relative">
                                    <div className="d-flex justify-content-between">
                                        <h6 className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name cursor-pointer">{mItem.author.name}<br /><span className="sub_author_name">@{mItem.author.username}</span> . <span className="sub_author_name">{moment(mItem.postTime).format("DD-MMM-YYYY")}</span></h6>
                                    </div>
                                    <p className="m-0 sub_author_name"></p>
                                </div>
                            </div>
                            <span className="text-dark font-weight-normal font-size-sm overflow-hidden text-ellipsis line-clamp-3" onClick={this.userOpen(mItem)}>
                                {mItem.content}
                            </span>
                        </div>
                    </div>
                </div>
                ) : <DataNotFound />
            }
            {/* <SeeMoreModal size={'lg'} showHide={this.state.show} handleClose={this.handleClose} title={'Overview'}>
                <MoreMentionPost {...this.props} />
            </SeeMoreModal> */}
            {this.state.isUserOpen ? <UserProfileModal size={'lg'} showHide={this.state.isUserOpen} handleClose={this.IsCloseUserProfile} title={''} userData={this.state.userData} trialAccessAPI={analyticsStatus.trialAccessAPI} /> : null}
            {this.state.show ? <AllPostsModal size={'lg'} onShow={this.state.show} onHide={this.handleClose} {...this.props} /> : null} 

            {this.state.isOpen ? <MentionPostsModal title={`Mentions Post`} size={'lg'} show={this.state.isOpen} onHide={this.userClose} userData={this.state.userData} trialAccessAPI={analyticsStatus.trialAccessAPI} /> : null}
        </>

    }
}
const mapStateToProps = (state) => {
    return {
        record_post_by_mention: state.wallRecordPostByMention.record_post_by_mention
    }
}
export default connect(mapStateToProps)(MentionPosts);

