import React, { Component } from "react";
import { connect } from "react-redux";
import moment from 'moment'
import { v4 as uuidv4 } from 'uuid';
import SVG from "react-inlinesvg";
import { Modal, Button } from "react-bootstrap";
import { toAbsoluteUrl } from "../../_helpers";
import { kFormatter } from '../../../utils'
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { topPostPerformanceForPopUp, pdfTopPerformancePostGenerate } from '../../../actions/analytics'
import { paginate } from '../../../utils/paginate'
import { TopPost } from "./TopPost";
import Loader from 'react-loader-spinner';
import saveAs from 'file-saver';
import TextContent from '../textContent'
import { MoreTopPerformancePostsSkeleton } from "../skeleton-loader-cards/PopupSkeletonCards";



class MoreTopPerformancePosts extends Component {
  state = {
    get_top_post_performance: null,
    isLoading: true,
    total: 0,
    limit: 10,
    offset: 0,
    paginateData: {},
    currentPage: 1,
    maxPage: 10,
    isPdfDownloading: false
  }

  componentDidMount() {
    const { get_top_post_performance, analyticwall, analyticsStatus } = this.props;
    const { limit, currentPage, maxPage } = this.state;
    if (get_top_post_performance && get_top_post_performance.data) {
      this.setState({ total: get_top_post_performance.total, paginateData: paginate(get_top_post_performance.total, currentPage, limit, maxPage) }, () => this.onLoadFilterData())
    }
  }
  onLoadFilterData = () => {
    const { analyticwall } = this.props;
    const { limit, offset } = this.state;
    const { selectedWallFeed, dateFilter, selectedFeed } = analyticwall;

    let wallData = null;
    if (selectedWallFeed && selectedWallFeed.wall) {
      wallData = selectedWallFeed.wall.id;
    }
    let startDate = dateFilter.startDate;
    let endDate = dateFilter.endDate;

    let feedIDs = [];
    if (selectedFeed && selectedFeed.length > 0) {
      selectedFeed.map((fItem) => {
        feedIDs.push(fItem.value);
      })
    }
    this.setState({ isLoading: true })
    topPostPerformanceForPopUp(wallData, moment(startDate).format("YYYY-MM-DD HH:mm:ss"), moment(endDate).format("YYYY-MM-DD HH:mm:ss"), feedIDs.join(), limit, offset).then((response) => {
      const { responseData } = response.data;
      this.setState({ get_top_post_performance: responseData, isLoading: false })
    })
      .catch((error) => {
        console.error("LAYOUT ISSUE", error)
      });

  }
  onUpdateDataLimit = event => {
    const { total, currentPage, limit, maxPage } = this.state;
    const pageCount = parseInt(currentPage) == 1 ? 0 : parseInt(currentPage);
    const offset = parseInt(event.target.value) * pageCount;
    this.setState({ limit: event.target.value, paginateData: paginate(total, currentPage, event.target.value, maxPage), offset: offset }, () => this.onLoadFilterData())
  }
  onUpdatePage = (page) => event => {
    const { total, limit, maxPage } = this.state;
    const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
    const offset = parseInt(limit) * pageCount;
    this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())

  }

  onPrevNavigateClick = event => {
    const { total, limit, maxPage, paginateData } = this.state;
    let page = 1;
    if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
      page = (paginateData.currentPage - 1)
    }
    if (page && page > 0) {
      const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
      const offset = parseInt(limit) * pageCount;
      this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())
    }
  }
  onNextNavigateClick = event => {
    const { total, limit, maxPage, paginateData } = this.state;
    let page = 1;
    if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
      page = (paginateData.currentPage + 1)
    }
    if (page && paginateData && Object.keys(paginateData).length > 0 && paginateData.totalPages >= page) {
      const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
      const offset = parseInt(limit) * pageCount;
      this.setState({ paginateData: paginate(total, page, limit, maxPage), offset: offset }, () => this.onLoadFilterData())
    }
  }

  clickToExport = event => {
    const { total, maxPage, paginateData, username } = this.state;
    let page = 1;
    if (paginateData && Object.keys(paginateData).length > 0 && paginateData.currentPage) {
      page = (paginateData.currentPage)
    }
    // const pageCount = parseInt(page) == 1 ? 0 : (parseInt(page) - 1);
    const offset = 0//parseInt(total) * pageCount;
    this.setState({ isPdfDownloading: true })
    pdfTopPerformancePostGenerate(offset, total).then((response) => {
      const file = new Blob([response.data], { type: 'application/pdf' });
      //const fileURL = URL.createObjectURL(file);
      saveAs(file, 'Top_Performance_Posts.pdf');
      this.setState({ isPdfDownloading: false })

    }).catch((error) => {
      console.error("pdfAnalyticGenerate issue", error)
      this.setState({ isPdfDownloading: false })
    });
  }

  render() {
    const { className, IsOpenUserProfile, isPostOpen, onShow, onHide, size, analyticsStatus } = this.props;
    const { isLoading, get_top_post_performance, total, limit, paginateData, currentPage, isPdfDownloading } = this.state;


    return (

      <Modal show={onShow}
        onHide={onHide}
        backdrop="static"
        keyboard={false}
        aria-labelledby="contained-modal-title-vcenter"
        centered
        size={size}>
        <Modal.Header closeButton>
          <h5 className="m-0">All Posts</h5>
        </Modal.Header>
                <Modal.Body className="p-md-3 p-3">
                <div className={isLoading? `d-none` : "d-flex align-items-center justify-content-end mb-3"}>
              <Button variant="outline-dark mr-4 px-4 px-sm-5 export-pdf d-flex align-items-center " size="sm"  onClick={!analyticsStatus.trialAccessAPI ? this.clickToExport : null}  className={(isPdfDownloading ? `d-flex pdf-loading` : `d-flex `, analyticsStatus.trialAccessAPI ? `disabled` : ``)} disabled={isPdfDownloading ? `disabled` : ''} >
                    <span className="svg-icon svg-icon-sm">
                      <SVG className="h-75 align-self-end" src={toAbsoluteUrl(
                        "/media/svg/export.svg"
                      )}
                      ></SVG>
                    </span>
                    <span className="btn_txt">{
                      isPdfDownloading ? <Loader type="ThreeDots" color="#333333" height={15} width={25} style={{ marginRight: 5 }} /> : `Export`
                    }</span>

                  </Button>
            </div>
          {isLoading ? <div className="h-100"><MoreTopPerformancePostsSkeleton /></div> :

            <div className="user_details_section">
              <div className="users_list">
                {(get_top_post_performance && get_top_post_performance.length > 0) ? get_top_post_performance.map((mItem, mIndex) => <div className="card px-sm-5 px-2 py-sm-3 py-2 mb-3">
                  <div className="row">
                    {mItem.type != 1 ? <div className="col-lg-4 user_profile_items cursor-pointer" onClick={isPostOpen(mItem)}>
                      <img src={mItem.mediaUrl ? mItem.mediaUrl : 'https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg'} className="img-fluid w-100 bg-light" onError={event => event.target.src = `https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg`}/>
                    </div> : null}
                    <div className={`col-lg-${mItem.type === 1 ? 12 : 8} user_profile_items_info pl-lg-5 mt-lg-0 mt-5`}>
                      <h6 className="m-0 d-flex justify-content-between user_profile_items_title cursor-pointer" onClick={IsOpenUserProfile(mItem)}>
                        <strong className="overflow-hidden text-nowrap text-ellipsis">{mItem.author.name}</strong>
                        <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                          `/media/svg/social-icons/${mItem.network.icon}.svg`
                        )}
                        ></SVG>
                      </h6>
                      <p className="cursor-pointer" onClick={IsOpenUserProfile(mItem)}><span>@{mItem.author.username}</span></p>
                      <div className="user_profile_others mt-lg-0 mt-2">
                        <div className="row">
                        <div className="col-sm-6">
                          <div className="user_profile_disc">
                          <TextContent fullContent={mItem.content} contentData={mItem.content} fullcontentData={mItem.content} />
                        </div>
                        </div>
                        <div className="col-sm-6">
                          <div className="user_profile_data ml-sm-7">
                          <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                            <span className="w-50 d-inline-block mr-1 text-nowrap">
                              <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                  `/media/svg/post-icon/impressions.svg`
                                )}
                                ></SVG>

                              </span> Impressions
                            </span>
                            <strong className="d-inline-block pl-sm-5">{kFormatter(parseInt(mItem.Impressions))}</strong>
                          </p>
                          <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                            <span className="w-50 d-inline-block mr-1 text-nowrap">
                              <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                  `/media/svg/post-icon/post_click.svg`
                                )}
                                ></SVG>
                              </span> Post Clicks
                            </span>
                            <strong className="d-inline-block pl-sm-5">{kFormatter(parseInt(mItem.postClick))}</strong>
                          </p>
                          <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                            <span className="w-50 d-inline-block mr-1 text-nowrap">
                              <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                  `/media/svg/post-icon/CTA.svg`
                                )}
                                ></SVG>
                              </span> Clicked CTA 
                            </span>
                            <strong className="d-inline-block pl-sm-5">{kFormatter(parseInt(mItem.postCTA))}</strong>
                          </p>
                          <p className="text-sm-left d-flex justify-content-sm-start justify-content-between">
                            <span className="w-50 d-inline-block mr-1 text-nowrap">
                              <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                                <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                  `/media/svg/post-icon/total_reach.svg`
                                )}
                                ></SVG>
                              </span> Total Reach
                            </span>
                            <strong className="d-inline-block pl-sm-5">{kFormatter(parseInt(mItem.potentialReach))}</strong>
                          </p>
                        </div>
                        </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>) : <DataNotFound />}

              </div>
            </div>
          }

        </Modal.Body>
        <Modal.Footer>
          <>
            {!isLoading && total > 0 && paginateData && Object.keys(paginateData).length > 0 ? <div className="w-100 d-flex justify-content-lg-between justify-content-center align-items-center flex-wrap px-lg-6 py-lg-4">
              <div className="d-flex flex-wrap py-2 mr-3 justify-content-sm-normal justify-content-center">

                <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onPrevNavigateClick}>
                  <i className="ki ki-bold-arrow-back icon-xs"></i>
                </a>

                {
                  (paginateData.pages && paginateData.pages.length > 0) ? paginateData.pages.map((item, pagesIndex) => {
                    return (parseInt(pagesIndex) < 5) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : (parseInt(paginateData.pages.length) - 2 == pagesIndex) ? <a className="btn btn-icon btn-sm border-0 btn-hover-primary mr-2 my-1">...</a> : (parseInt(paginateData.pages.length) - 1 == pagesIndex) ? <a onClick={this.onUpdatePage(item)} className={`btn btn-icon btn-sm border-0 btn-light  btn-hover-primary ${paginateData.currentPage == item ? `active` : ``}  mr-2 my-1`}>{item}</a> : null

                  }) : null
                }
                <a className="btn btn-icon btn-sm btn-light btn-hover-primary mr-2 my-1" onClick={this.onNextNavigateClick}>
                  <i className="ki ki-bold-arrow-next icon-xs"></i>
                </a>
              </div>
              <div className="d-flex align-items-center py-1 shwoing-filter">
                <select
                  className="form-control form-control-sm font-weight-bold mr-4 border-0 bg-light false"
                  style={{ width: 75 }}
                  onChange={this.onUpdateDataLimit}
                  value={limit}
                >

                  <option className="" value={10}>10</option>
                  <option className="" value={20}>20</option>
                  <option className="" value={50}>50</option>
                </select>
                <span className="react-bootstrap-table-pagination-total">
                  &nbsp;Showing rows {paginateData.currentPage} to&nbsp;{paginateData.totalPages} of&nbsp;{total}
                </span>
              </div>
            </div> : null}

          </>

        </Modal.Footer>
      </Modal>




    );
  }
}
const mapStateToProps = (state) => {
  return {
    analyticwall: state.analyticwall,
    analyticsStatus: state.analyticsStatus
  }
}
export default connect(mapStateToProps)(MoreTopPerformancePosts);