/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import { connect } from "react-redux";
import {
    Button,
    Overlay,
    Tooltip,
    OverlayTrigger,
    Tab, Tabs, Nav,
    ButtonToolbar
} from "react-bootstrap";
import ReactApexChart from 'react-apexcharts'
import { v4 as uuidv4 } from 'uuid';
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { getRandomArbitrary } from '../../../utils'
import { PostGraphSkeleton } from "../skeleton-loader-cards/SkeletonCards";


class PostGraph extends Component {
    state = {
        record_type_by_post_network: null,
        isLoading: true
    }
    componentDidMount() {
        const { record_type_by_post_network } = this.props;
        if (record_type_by_post_network) {
            this.setState({ record_type_by_post_network: record_type_by_post_network }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
        }
    }
    static getDerivedStateFromProps(props, state) {
        if (props.record_type_by_post_network !== state.record_type_by_post_network) {
            return {
                record_type_by_post_network: props.record_type_by_post_network
            };
        }
        return null;
    }
    onUpdateData = (Data) => {
        const ChartDataValue = (Data.length > 0 && Data[0] && Data[0].data) ? Data[0].data : []
        const ChartDataValueTotal = (ChartDataValue && ChartDataValue.length > 0) ? ChartDataValue.reduce((a, b) => a + b, 0) : 0;
        const ChartData = {
            series: ChartDataValue,
            options: {
                dataLabels: {
                    enabled: false
                },
                legend: {
                    show: false,
                },
                colors: ['#FC5362', '#FEC737', '#5096FF'],
                chart: {
                    type: 'donut',
                    animations: {
                        enabled: true,
                        easing: 'easeinout',
                        speed: 800,
                        animateGradually: {
                            enabled: true,
                            delay: 1200
                        },
                        dynamicAnimation: {
                            enabled: true,
                            speed: 350
                        }
                    }
                },
                labels: Data[0] && Data[0].labels ? Data[0].labels : [],
                plotOptions: {
                    pie: {
                        size: 5,
                        donut: {
                            legend: {
                                show: false,
                            },
                            labels: {
                                show: true,
                                total: {
                                    showAlways: true,
                                    show: true
                                }
                            }
                        }
                    }
                }

            }
        };
        const PostDataValue = (Data.length > 0 && Data[1] && Data[1].data) ? Data[1].data : []
        const postData = {

            series: PostDataValue,
            options: {
                dataLabels: {
                    enabled: false
                },
                legend: {
                    show: false,
                },
                colors: Data[1] && Data[1].colors ? Data[1].colors : [],
                chart: {
                    type: 'donut',
                },
                plotOptions: {
                    pie: {
                        donut: {
                            legend: {
                                show: false,
                            },
                            labels: {
                                show: true,
                                total: {
                                    showAlways: true,
                                    show: true
                                }
                            }
                        }
                    }
                },
                labels: Data[1] && Data[1].labels ? Data[1].labels : []
            }
        };
        return { ChartDataValue, ChartData, PostDataValue, postData, ChartDataValueTotal };
    }

    render() {
        const { pageRequest } = this.props
        const { isLoading, record_type_by_post_network } = this.state;
        const Data = record_type_by_post_network;
        const { ChartDataValue, ChartData, PostDataValue, postData, ChartDataValueTotal } = this.onUpdateData(Data)
        return (
            <div className="card card-custom card-stretch gutter-b">
                {pageRequest.loader ? <PostGraphSkeleton /> :
                    <Tabs defaultActiveKey="posttype" id="postgraph" className="nav-tabs-line text-dark font-size-h5 flex-nowrap overflow-x-auto">
                        <Tab eventKey="posttype" title="Post Type">
                            {pageRequest.loader ? <DataLoading /> : <div className="card-body h-100">
                                {
                                    (ChartDataValueTotal != null && ChartDataValueTotal == 0) ? <DataNotFound /> : <>
                                        <div className="donut_chart_wrapper">
                                            <ReactApexChart key={uuidv4()} options={ChartData.options} series={ChartData.series} type="donut" />
                                        </div>
                                        <div className="post_type_score w-100">
                                            <div className="d-flex align-items-center justify-content-between w-100">
                                                {Data[0] && Data[0].labels.length > 0 ? Data[0].labels.map((lItem, index) => <div key={uuidv4()} className="d-flex flex-column text-center m-auto">
                                                    <small>{lItem}</small>
                                                    <h3 className="text-center">{Data[0].data[index]}</h3>
                                                    <div className="post_score_border"></div>
                                                </div>) : null
                                                }
                                            </div>
                                        </div>
                                    </>} </div>}
                        </Tab>


                        <Tab eventKey="postnetwork" title="Post Network">
                            {pageRequest.loader ? <DataLoading /> : <div className="card-body h-100">
                                {postData.series && postData.series.length > 0 ? <>
                                    <div className="donut_chart_wrapper">
                                        <ReactApexChart key={uuidv4()} options={postData.options} series={postData.series} type="donut" />
                                    </div>
                                    <div className="post_type_score w-100">
                                        <div className="d-flex align-items-center justify-content-center w-100 flex-wrap">
                                            {
                                                Data[1] && Data[1].labels && Data[1].labels.length > 0 ? Data[1].labels.map((lItem, index) => <div key={uuidv4()} className="d-flex flex-column text-center mx-2 mb-2">
                                                    <small>{lItem}</small>
                                                    <h3 className="text-center">{Data[1].data[index]}</h3>
                                                    <div className="post_score_border" style={{ backgroundColor: Data[1].colors[index] }}></div>
                                                </div>) : null
                                            }
                                        </div>
                                    </div>
                                </> : <DataNotFound />}</div>}
                        </Tab>
                    </Tabs>}
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        record_type_by_post_network: state.wallRecordTypeByPostNetwork.record_type_by_post_network,
        pageRequest: state.wallRecordTypeByPostNetwork
    }
}
export default connect(mapStateToProps)(PostGraph);



