/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import { connect } from "react-redux";
import ReactApexChart from 'react-apexcharts'
import { v4 as uuidv4 } from 'uuid';
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { PostPlatformsSkeleton } from "../skeleton-loader-cards/SkeletonCards";

class PostPlatforms extends Component {
  state = {
    record_post_click_by_platforms: null,
    isLoading: true,
    data: null
  }
  componentDidMount() {
    const { record_post_click_by_platforms } = this.props;
    if (record_post_click_by_platforms) {
      this.setState({ record_post_click_by_platforms: record_post_click_by_platforms, data: this.onLoadChartUpdate(record_post_click_by_platforms) }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
    }
  }
  static getDerivedStateFromProps = (props, state) => {
    if (props.record_post_click_by_platforms !== state.record_post_click_by_platforms) {
      return {
        record_post_click_by_platforms: props.record_post_click_by_platforms,
        data: this.onLoadChart(props.record_post_click_by_platforms)
      };
    }
    return null;
  }
  static onLoadChart = (record_post_click_by_platforms) => {

    const chartData = record_post_click_by_platforms && record_post_click_by_platforms.length > 0 ? record_post_click_by_platforms[0] : [];
    let resultData = chartData.data && chartData.data.length > 0 ? chartData.data.map((x) => parseInt(x, 10)) : [];
    const data = {

      series: resultData,
      options: {
        dataLabels: {
          enabled: false,
          style: {
            colors: chartData.color
          }
        },
        legend: {
          show: true,
          showForSingleSeries: false,
          showForNullSeries: true,
          showForZeroSeries: true,
          position: 'right',
          horizontalAlign: 'right',
          floating: false,
          fontSize: '16x',
          fontFamily: 'Inter',
          fontWeight: 500,
          formatter: function (seriesName, opts) {
            return [chartData.labels[opts.seriesIndex], " : ", opts.w.globals.series[opts.seriesIndex]]
          },
          inverseOrder: false,
          width: undefined,
          height: undefined,
          tooltipHoverFormatter: undefined,
          customLegendItems: [],
          offsetX: 0,
          offsetY: 0,
          labels: {
            colors: undefined,
            useSeriesColors: false
          },
          markers: {
            width: 12,
            height: 12,
            strokeWidth: 0,
            strokeColor: '#fff',
            fillColors: chartData.color,
            radius: 0,
            customHTML: undefined,
            onClick: undefined,
            offsetX: 0,
            offsetY: 0
          },
          itemMargin: {
            horizontal: 5,
            vertical: 0
          },
          onItemClick: {
            toggleDataSeries: true
          },
          onItemHover: {
            highlightDataSeries: true
          },
        },
        chart: {
          type: 'donut',
        },
        labels: chartData.labels,

        colors: chartData.color,
        plotOptions: {
          pie: {
            donut: {

              legend: {
                show: false,
              },
              labels: {
                style: {
                  colors: chartData.color
                },
                show: true,
                total: {
                  showAlways: true,
                  show: true
                }
              }
            }
          }
        },
        responsive: [{
          breakpoint: 1600,
          options: {
            dataLabels: {
              enabled: false
            },
            legend: {
              position: 'bottom',
              horizontalAlign: 'bottom',
            }
          }
        }]
      }

    };
    return data
  }
  onLoadChartUpdate = (record_post_click_by_platforms) => {

    const chartData = record_post_click_by_platforms && record_post_click_by_platforms.length > 0 ? record_post_click_by_platforms[0] : [];
    let resultData = chartData.data && chartData.data.length > 0 ? chartData.data.map((x) => parseInt(x, 10)) : [];
    const data = {

      series: resultData,
      options: {
        dataLabels: {
          enabled: false,
          style: {
            colors: chartData.color
          }
        },
        legend: {
          show: true,
          showForSingleSeries: false,
          showForNullSeries: true,
          showForZeroSeries: true,
          position: 'right',
          horizontalAlign: 'right',
          floating: false,
          fontSize: '16x',
          fontFamily: 'Inter',
          fontWeight: 500,
          formatter: function (seriesName, opts) {
            return [chartData.labels[opts.seriesIndex], " : ", opts.w.globals.series[opts.seriesIndex]]
          },
          inverseOrder: false,
          width: undefined,
          height: undefined,
          tooltipHoverFormatter: undefined,
          customLegendItems: [],
          offsetX: 0,
          offsetY: 0,
          labels: {
            colors: undefined,
            useSeriesColors: false
          },
          markers: {
            width: 12,
            height: 12,
            strokeWidth: 0,
            strokeColor: '#fff',
            fillColors: chartData.color,
            radius: 0,
            customHTML: undefined,
            onClick: undefined,
            offsetX: 0,
            offsetY: 0
          },
          itemMargin: {
            horizontal: 5,
            vertical: 0
          },
          onItemClick: {
            toggleDataSeries: true
          },
          onItemHover: {
            highlightDataSeries: true
          },
        },
        chart: {
          type: 'donut',
        },
        labels: chartData.labels,

        colors: chartData.color,
        plotOptions: {
          pie: {
            donut: {

              legend: {
                show: false,
              },
              labels: {
                style: {
                  colors: chartData.color
                },
                show: true,
                total: {
                  showAlways: true,
                  show: true
                }
              }
            }
          }
        }
      },
    };
    return data
  }

  render() {
    const { className, pageRequest } = this.props
    const { isLoading, record_post_click_by_platforms, data } = this.state;

    return (
      <div className={`card card-custom ${className}`}>
        {pageRequest.loader ? <PostPlatformsSkeleton /> : <>
          <div className="card-header min-h-auto py-3">
            <div className="card-title">
              <h5 className="card-label font-weight-bolder">Post click by Platforms</h5>
            </div>
          </div>
          <div className="card-body p-0 mb-5">
            <div className="donut_chart_wrapper_post">
              {isLoading ? <DataLoading /> : (record_post_click_by_platforms && record_post_click_by_platforms.length > 0 && record_post_click_by_platforms[0].data && record_post_click_by_platforms[0].data.length > 0 && data) ? <ReactApexChart key={uuidv4()} className="post_click_platform" options={data.options} series={data.series} type="donut" height={'100%'} /> : <DataNotFound />}
            </div>
          </div>
        </>}
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    record_post_click_by_platforms: state.wallRecordPostClickByPlatforms.record_post_click_by_platforms,
    pageRequest: state.wallRecordPostClickByPlatforms
  }
}
export default connect(mapStateToProps)(PostPlatforms);




