import { all } from "redux-saga/effects";
import { combineReducers } from "redux";

import * as auth from "../app/modules/Auth/_redux/authRedux";
import wall from './wallReducer'
import authorized from './authReducer'
import analyticwall from './analyticWallReducer'
import analytics from './analyticsReducer'
import analyticsStatus from './analyticsStatusReducer'

import postClickPlatforms from './postClickPlatforms'
import topPostPerformance from './topPostPerformance'
import wallRecordCounts from './wallRecordCounts'
import wallRecordInfluentialContributors from './wallRecordInfluentialContributors'
import wallRecordMostActiveUsers from './wallRecordMostActiveUsers'
import wallRecordOverview from './wallRecordOverview'
import wallRecordPostByHighestShared from './wallRecordPostByHighestShared'
import wallRecordPostByMention from './wallRecordPostByMention'
import wallRecordPostClickByPlatforms from './wallRecordPostClickByPlatforms'
import wallRecordPostOverview from './wallRecordPostOverview'
import wallRecordSentimentByPostStatistics from './wallRecordSentimentByPostStatistics'
import wallRecordTypeByPostNetwork from './wallRecordTypeByPostNetwork'
import wallRecordWordCloud from './wallRecordWordCloud'
import wallRefresh from './refreshReducer'
import errorReducer from './errorReducer'

export const rootReducer = combineReducers({
  auth: auth.reducer,
  wall: wall,
  authorized: authorized,
  analyticwall: analyticwall,
  analytics: analytics,
  analyticsStatus: analyticsStatus,
  postClickPlatforms: postClickPlatforms,
  topPostPerformance: topPostPerformance,
  wallRecordCounts: wallRecordCounts,
  wallRecordInfluentialContributors: wallRecordInfluentialContributors,
  wallRecordMostActiveUsers: wallRecordMostActiveUsers,
  wallRecordOverview: wallRecordOverview,
  wallRecordPostByHighestShared: wallRecordPostByHighestShared,
  wallRecordPostByMention: wallRecordPostByMention,
  wallRecordPostClickByPlatforms: wallRecordPostClickByPlatforms,
  wallRecordPostOverview: wallRecordPostOverview,
  wallRecordSentimentByPostStatistics: wallRecordSentimentByPostStatistics,
  wallRecordTypeByPostNetwork: wallRecordTypeByPostNetwork,
  wallRecordWordCloud: wallRecordWordCloud,
  wallRefresh: wallRefresh,
  errorUpdate: errorReducer

});

export function* rootSaga() {
  yield all([auth.saga()]);
}
