import React, { Component } from "react";
import { connect } from "react-redux";
import { getWallFeeds, getTrialAnalyticsData, getAnalytcialStatus, TAGGBOX_USER_LOGIN } from '../actions/analytics'
import RoutesExtends from './RoutesExtends'

class AnalyticStatus extends Component {
    componentWillMount() {
        const { wall } = this.props;
        this.props.getAnalytcialStatus(wall.wallID)
        this.props.getWallFeeds(wall.ownerID)
    }
    componentDidMount() {
        const { analyticsStatus } = this.props;
        if (analyticsStatus.trialAccessAPI) this.props.getTrialAnalyticsData()
    }
    render() {
        return <RoutesExtends {...this.props} />
    }
}
const mapStateToProps = (state) => {
    return {
        analyticsStatus: state.analyticsStatus,
        wall: state.wall,
        analyticwall: state.analyticwall
    }
}
const mapDispatchToProps = dispatch => {
    return {
        getAnalytcialStatus: (wallID) => dispatch(getAnalytcialStatus(wallID)),
        getWallFeeds: (userID) => dispatch(getWallFeeds(userID)),
        getTrialAnalyticsData: () => dispatch(getTrialAnalyticsData()),
        TAGGBOX_USER_LOGIN: () => dispatch(TAGGBOX_USER_LOGIN()),


    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AnalyticStatus);