/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid,no-undef */
import React, { Component } from "react";
import { connect } from 'react-redux';
import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../../../_helpers";
import { HOST_URL, HOST_URL_TAGGSHOP } from '../../../../../actions/api'
import { logOutRequest } from '../../../../../actions/analytics'

const UpdatePremium = () => <span className="premium-user-sign user-sign-dropdown-sign">
  <img src={toAbsoluteUrl("/media/images/Star.png")} />
</span >

class QuickUser extends Component {
  // const history = useHistory();

  logoutClick = event => {
    const { history } = this.props;
    logOutRequest(history);
  }
  checkStarVisible = () => {
    const { authorized } = this.props;
    var isVisible = false;
    if (authorized.userData && authorized.userData.userPlanDetails && authorized.userData.userPlanDetails.planIds) {
      authorized.userData.userPlanDetails.planIds.map((item) => {
        if (!["1", "12", "14", "53"].includes(item) && !isVisible) {
          isVisible = true;

        }

      })
      return isVisible;
    }
    else return isVisible;

  }
  copyTxt = (text) => event => {
    var inp = document.createElement('input');
    document.body.appendChild(inp)
    inp.value = text
    inp.select();
    document.execCommand('copy', false);
    inp.remove();
  }
  render() {
    const { authorized } = this.props;
    return (
      <div id="kt_quick_user" className="offcanvas offcanvas-right offcanvas p-10">
        {
          (authorized.userData && authorized.userData.role && (authorized.userData.role == 1 || authorized.userData.role == 3)) ? <a href="/users/userLogin" className="whitelabelanchor btn btn-sm btn-secondary font-weight-bolder py-2 px-5 d-block mb-6">Back to Admin</a> : null}
        <div className="offcanvas-header d-flex align-items-center justify-content-between pb-5">
          <h3 className="font-weight-bold m-0">
            Profile
      </h3>
          <a href="#" className="btn btn-xs btn-icon btn-light btn-hover-primary" id="kt_quick_user_close">
            <i className="ki ki-close icon-xs text-muted"></i>
          </a>
        </div>

        <div
          className="offcanvas-content pr-5 mr-n5"
        >
          <div className="d-flex align-items-center mt-5">
            <div
              className="symbol symbol-100 mr-5 symbol-light-warning"
            >
              <span className="symbol-label font-size-h1 font-weight-bold text-uppercase">
                {
                  (authorized.userData && authorized.userData.username) ? authorized.userData.username.charAt(0) : ""
                }
              </span>
              {this.checkStarVisible() ? <UpdatePremium /> : null}
            </div>
            <div className="w-100">
              <a onClick={this.copyTxt(authorized.userData.username)}
                href="#"
                className="font-weight-bold font-size-h5 text-dark-75 text-hover-primary"
              >
                {authorized.userData.username}
              </a>
              <div className="navi mt-2">
                <a href="#" className="navi-item">
                  <span className="navi-link p-0 pb-2 align-items-start">
                    <span className="navi-icon mr-1">
                      <span className="svg-icon svg-icon-primary">
                        <SVG
                          src={toAbsoluteUrl(
                            "/media/svg/dashboard/Mail-notification.svg"
                          )}
                        ></SVG>
                      </span>
                    </span>
                    <span className="navi-text text-muted text-hover-primary user_email_Id">
                      {authorized.userData.email}
                    </span>
                  </span>
                </a>
              </div>
              {/* <Link to="/logout" className="btn btn-light-primary btn-bold">
            Sign Out
          </Link> */}
              {/* <button className="whitelabelanchor btn btn-sm btn-secondary font-weight-bolder py-2 px-5" onClick={this.logoutClick}>Sign out</button> */}
              <button className="whitelabelanchor btn btn-sm btn-secondary font-weight-bolder py-2 px-5" onClick={this.logoutClick}>Sign out</button>
            </div>
          </div>

          <div className="separator separator-dashed mt-8 mb-5" />

          <div className="navi navi-spacer-x-0 p-0">
            <a href={`${HOST_URL_TAGGSHOP}profile`} className="navi-item">
              <div className="navi-link">
                <div className="symbol symbol-40 bg-light mr-3">
                  <div className="symbol-label">
                    <span className="svg-icon svg-icon-md svg-icon-success">
                      <SVG
                        src={toAbsoluteUrl(
                          "/media/svg/dashboard/User.svg"
                        )}
                      ></SVG>
                    </span>
                  </div>
                </div>
                <div className="navi-text">
                  <div className="font-weight-bold">My Profile</div>
                  <div className="text-muted">
                    Account settings
            </div>
                </div>
              </div>
            </a>

            <a href={`${HOST_URL_TAGGSHOP}notifications`} className="navi-item">
              <div className="navi-link">
                <div className="symbol symbol-40 bg-light mr-3">
                  <div className="symbol-label">
                    <span className="svg-icon svg-icon-md svg-icon-warning">
                      <SVG
                        src={toAbsoluteUrl(
                          "/media/svg/dashboard/Mail-box.svg"
                        )}
                      ></SVG>
                    </span>
                  </div>
                </div>
                <div className="navi-text">
                  <div className="font-weight-bold">My Notifications</div>
                  <div className="text-muted">All Notifications</div>
                </div>
              </div>
            </a>

            <a href={`${HOST_URL_TAGGSHOP}transactions`} className="navi-item">
              <div className="navi-link">
                <div className="symbol symbol-40 bg-light mr-3">
                  <div className="symbol-label">
                    <span className="svg-icon svg-icon-md svg-icon-danger">
                      <SVG
                        src={toAbsoluteUrl(
                          "/media/svg/dashboard/Shield-user.svg"
                        )}
                      ></SVG>
                    </span>
                  </div>
                </div>
                <div className="navi-text">
                  <div className="font-weight-bold">My Account</div>
                  <div className="text-muted">Account info, Plans and more</div>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    authorized: state.authorized,
  }
}

export default connect(mapStateToProps)(QuickUser);