/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import { connect } from "react-redux";
import { v4 as uuidv4 } from 'uuid';
import { Tab, Tabs } from "react-bootstrap";
import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../_helpers";
import SentimentsPost from './SentimentsPost'
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { SentimentsSkeleton } from "../skeleton-loader-cards/SkeletonCards";

const SentimentsData = [{ icon: "/media/emojis/emoji1.svg", value: 30, color: 'red' }, { icon: "/media/emojis/emoji2.svg", value: 25, color: 'green' }, { icon: "/media/emojis/emoji3.svg", value: 19, color: 'blue' }, { icon: "/media/emojis/emoji4.svg", value: 29, color: 'yellow' }, { icon: "/media/emojis/emoji5.svg", value: 45, color: 'orange' }]




class Sentiments extends Component {
    state = {
        SentimentsUpdateData: null,
        isLoading: true
    }
    componentDidMount() {
        const { record_sentiment_by_post_statistics } = this.props;
        if (record_sentiment_by_post_statistics) {
            this.setState({ SentimentsUpdateData: record_sentiment_by_post_statistics }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
        }
    }
    static getDerivedStateFromProps(props, state) {
        if (props.record_sentiment_by_post_statistics !== state.SentimentsUpdateData) {
            return {
                SentimentsUpdateData: props.record_sentiment_by_post_statistics
            };
        }
        return null;
    }


    render() {
        const { isLoading, SentimentsUpdateData } = this.state;
        const { analyticsStatus, pageRequest } = this.props;
        return (
            <div className="card card-custom card-stretch gutter-b">
                {pageRequest.loader ? <SentimentsSkeleton /> :
                        <Tabs defaultActiveKey="sstatistics" id="sentiment" className="nav-tabs-line text-dark font-size-h5 flex-nowrap overflow-x-auto">
                            <Tab eventKey="sstatistics" title="Sentiment Statistics">
                                <div className="card-body">
                                    {
                                        pageRequest.loader ? <DataLoading /> : (SentimentsUpdateData.length > 0 && SentimentsUpdateData[0].data && SentimentsUpdateData[0].data[0] && Object.keys(SentimentsUpdateData[0].data[0]).length > 0) ? Object.keys(SentimentsUpdateData[0].data[0]).map((item, index) => {
                                            let reItem = {}
                                            let value = SentimentsUpdateData[0].data[0][item] ? SentimentsUpdateData[0].data[0][item] : 0;
                                            let total = [SentimentsUpdateData[0].data[0]['negative'], SentimentsUpdateData[0].data[0]['neutral'], SentimentsUpdateData[0].data[0]['positive'], SentimentsUpdateData[0].data[0]['strongPos'], SentimentsUpdateData[0].data[0]['strongneg']].reduce((a, b) => a + b, 0);
                                            if (item == "strongPos") reItem = SentimentsData[0]
                                            else if (item == "positive") reItem = SentimentsData[1]
                                            else if (item == "neutral") reItem = SentimentsData[2]
                                            else if (item == "negative") reItem = SentimentsData[3]
                                            else if (item == "strongneg") reItem = SentimentsData[4]
                                            let percent = parseInt(value) > 0 ? (value / total * 100) : 0;
                                            percent = percent.toFixed(1);
                                            return <div key={uuidv4()} className="d-flex align-items-center my-7 w-100">
                                                <div className="svg-icon svg-icon-3x mr-3"><SVG
                                                    src={toAbsoluteUrl(reItem.icon)}
                                                ></SVG></div>
                                                <div className={`progress_sentiment progress w-100 pro_${index}`} now={value} label={`${value}`} >
                                                    {(percent > 0) ?
                                                        <div role="progressbar" className="progress-bar" aria-valuenow={value} aria-valuemin="0" aria-valuemax="100" style={{ width: `${percent}%` }}>
                                                            <div className="progress_label">
                                                                <span>{percent}%({value})</span>
                                                            </div>
                                                        </div>
                                                        : ''
                                                    }
                                                </div>
                                            </div>
                                        }) : <DataNotFound />
                                    }
                                </div>
                            </Tab>
                            <Tab eventKey="sposts" title="Sentiment Posts">
                                <div className="card-body" style={{ padding: 0 }}>
                                    {pageRequest.loader ? <DataLoading /> : SentimentsUpdateData && SentimentsUpdateData.length > 0 && SentimentsUpdateData[1] ? <SentimentsPost Data={SentimentsUpdateData[1]} className={`card-stretch gutter-b`} analyticsStatus={analyticsStatus} /> : <DataNotFound />}

                                </div>

                            </Tab>
                        </Tabs>}
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        record_sentiment_by_post_statistics: state.wallRecordSentimentByPostStatistics.record_sentiment_by_post_statistics,
        pageRequest: state.wallRecordSentimentByPostStatistics,
    }
}
export default connect(mapStateToProps)(Sentiments);