/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { useMemo, useLayoutEffect, useEffect, useState } from "react";
import objectPath from "object-path";
import moment from 'moment';
import Loader from 'react-loader-spinner';
import saveAs from 'file-saver';
import ReactToPdf from 'react-to-pdf'
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import DateRangePicker from 'react-bootstrap-daterangepicker';
import 'bootstrap-daterangepicker/daterangepicker.css';
import { useLocation, useHistory } from "react-router-dom";
import { getBreadcrumbsAndTitle, useSubheader } from "../../_core/MetronicSubheader";
import { useHtmlClassService } from "../../_core/MetronicLayout"
import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../../_helpers";
import { updateDateFilter, refreshButton, pdfAnalyticGenerate } from '../../../../actions/analytics'
import FeedsList from './feedsComponent'

import {
  Button,
  Tooltip,
  OverlayTrigger
} from "react-bootstrap";
import { iter } from "@amcharts/amcharts4/core";

export function SubHeader(props) {
  const history = useHistory();
  const dispatch = useDispatch()
  const uiService = useHtmlClassService();
  const location = useLocation();
  const subheader = useSubheader();
  const [startDate, setStartDate] = useState(moment().subtract(29, 'days').toDate());
  const [endDate, setEndDate] = useState(moment().format("DD-MM-YYYY"));
  const [maxDate, setMaxDate] = useState(moment().endOf('month').format("DD-MM-YYYY"));
  const [isPdfDownloading, setIsPdfDownloading] = useState(false);
  const [isRefresh, setIsRefresh] = useState(false);
  const [isVisible, setIsVisible] = useState(false);
  const [isActive, setActive] = useState(false);

  const handleToggle = () => {
    setActive(!isActive);
    setTimeout(() => setIsVisible(!isVisible), 30)

  };

  const { analyticwall, analyticsStatus, wallRefresh } = useSelector(
    ({ auth, analyticwall, analyticsStatus, wallRefresh }) => ({
      analyticwall: analyticwall,
      analyticsStatus: analyticsStatus,
      wallRefresh: wallRefresh
    }),
    shallowEqual
  );


  const layoutProps = useMemo(() => {
    return {
      config: uiService.config,
      subheaderMobileToggle: objectPath.get(
        uiService.config,
        "subheader.mobile-toggle"
      ),
      subheaderCssClasses: uiService.getClasses("subheader", true),
      subheaderContainerCssClasses: uiService.getClasses(
        "subheader_container",
        true
      )
    };
  }, [uiService]);

  useLayoutEffect(() => {
    const aside = getBreadcrumbsAndTitle("kt_aside_menu", location.pathname);
    const header = getBreadcrumbsAndTitle("kt_header_menu", location.pathname);
    const breadcrumbs = (aside && aside.breadcrumbs.length > 0) ? aside.breadcrumbs : header.breadcrumbs;
    subheader.setBreadcrumbs(breadcrumbs);
    subheader.setTitle((aside && aside.title && aside.title.length > 0) ? aside.title : header.title);
    // eslint-disable-next-line
  }, [location.pathname]);

  // Do not remove this useEffect, need from update title/breadcrumbs outside (from the page)
  useEffect(() => { }, [subheader]);


  const handleOnApply = (event, picker) => {
    dispatch(updateDateFilter({ startDate: picker.startDate, endDate: picker.endDate }, analyticsStatus.trialAccessAPI))
    setStartDate(picker.startDate.format("DD-MM-YYYY"))
    setEndDate(picker.endDate.format("DD-MM-YYYY"))
  }
  const onClickRefreshButton = event => {
    //setIsRefresh(true)
    //history.push(window.location.href)
    dispatch(refreshButton(analyticsStatus.trialAccessAPI))
    //setTimeout(() => setIsRefresh(false), 2000)
  }
  const [state, setState] = useState({
    start: moment().subtract(29, 'days'),
    end: moment(),
  });
  const handleCallback = (start, end) => {
    dispatch(updateDateFilter({ startDate: start, endDate: end }, analyticsStatus.trialAccessAPI))
    setStartDate(start.format("DD-MM-YYYY"))
    setEndDate(end.format("DD-MM-YYYY"))
  };

  const label = 'This Month';
  const pdfClickGenerate = event => {
    setIsPdfDownloading(true)
    pdfAnalyticGenerate(analyticsStatus.trialAccessAPI).then((response) => {
      const file = new Blob([response.data], { type: 'application/pdf' });
      const fileURL = URL.createObjectURL(file);
      window.open(fileURL, "_blank");
      saveAs(file, 'Taggshop-Analytics.pdf');
      setIsPdfDownloading(false)

    }).catch((error) => {
      console.error("pdfAnalyticGenerate issue", error)
      setIsPdfDownloading(false)
    });

  }

  const options = {
    orientation: 'landscape',
    unit: 'in',
    format: [4, 2]
  };
  document.addEventListener("click", (event) => {
    const more_Action = document.querySelector("#sub_header_more_action")
    if (isActive && more_Action && more_Action.classList && more_Action.classList.contains("active") && more_Action.getAttribute("data-visible") == "true") {
      more_Action.classList.remove("active")
      setActive(false);
      setIsVisible(false)
    }
  })
  return (
    <div
      id="kt_subheader"
      className={`subheader py-2 py-lg-4   ${layoutProps.subheaderCssClasses}`}
    >
      <div
        className={`${layoutProps.subheaderContainerCssClasses} d-flex flex-wrap align-items-center justify-content-between flex-wrap flex-sm-nowrap`}
      >
        {/* Info */}
        <div className="d-flex align-items-center flex-wrap mr-1 flex-grow-1">
          {layoutProps.subheaderMobileToggle && (
            <button
              className="burger-icon burger-icon-left mr-4 d-inline-block d-lg-none"
              id="kt_subheader_mobile_toggle"
            >
              <span />
            </button>
          )}

          <div className="d-flex align-items-baseline mr-5 mb-md-0 mb-3">
            <h4 className="text-dark font-weight-boldest my-2 mr-5 text-nowrap overflow-hidden text-ellipsis wall_name">
              Analytics
            </h4>

          </div>


        </div>

        {/* Toolbar */}
        <div className="d-flex align-items-center filter_btns flex-shrink-0 justify-content-end mobile-d-block ">
          <div className="d-flex align-items-center filter_btn_wrap">
            <FeedsList />
            <button type="button" className="btn btn-hover-light-secondary btn-sm btn-icon mr-1 btn-more d-none" onClick={handleToggle}>
              <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                <SVG className="h-75 align-self-end" src={toAbsoluteUrl(
                  "/media/svg/ellipsis-new.svg"
                )}></SVG>
              </span>
            </button>
          </div>

          <div id="sub_header_more_action" data-visible={isVisible} className={isActive ? "d-flex align-items-center mobile-more-option active" : "d-flex align-items-center mobile-more-option"}>

            <OverlayTrigger placement='bottom' overlay={
              <Tooltip>Print</Tooltip>
            }
            >
              <button type="button" className="btn btn-hover-light-secondary btn-sm btn-icon mr-1" onClick={event => window.print()}>
                <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                  <SVG className="h-75 align-self-end" src={toAbsoluteUrl(
                    "/media/svg/print.svg"
                  )}
                  ></SVG>
                </span>
              </button>
            </OverlayTrigger>
            {/* <Button variant="outline-dark mx-4 px-4 px-sm-5" size="sm" onClick={event => window.print()}>
              <span className="svg-icon svg-icon-sm">
                <SVG className="h-75 align-self-end" src={toAbsoluteUrl(
                  "/media/svg/print.svg"
                )}
                ></SVG>
              </span>
              <span className="btn_txt">Print</span></Button> */}
            {/* <Button variant="outline-dark mr-4 px-4 px-sm-5" size="sm" onClick={pdfClickGenerate} className={isPdfDownloading ? `pdf-loading` : ``} disabled={isPdfDownloading ? `disabled` : ''} >
              <span className="svg-icon svg-icon-sm">
                <SVG className="h-75 align-self-end" src={toAbsoluteUrl(
                  "/media/svg/export.svg"
                )}
                ></SVG>
              </span>
              <span className="btn_txt">
                {
                  isPdfDownloading ? <Loader type="ThreeDots" color="#ffffff" height={18} width={18} style={{ marginRight: 5 }} /> : `Export`
                }
              </span></Button> */}



            <OverlayTrigger
              placement='bottom'
              overlay={
                <Tooltip>Export</Tooltip>
              }
            >
              <button type="button" onClick={pdfClickGenerate} className={isPdfDownloading ? `pdf-loading btn btn-hover-light-secondary btn-sm btn-icon mr-1` : `btn btn-hover-light-secondary btn-sm btn-icon mr-1`} disabled={isPdfDownloading ? `disabled` : ''} >
                <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                  {
                    isPdfDownloading ? <Loader type="ThreeDots" color="#333333" height={18} width={18} /> : <SVG className="h-75 align-self-end" src={toAbsoluteUrl(
                      "/media/svg/export.svg"
                    )}
                    ></SVG>
                  }

                </span>
              </button>
            </OverlayTrigger>


            <DateRangePicker onApply={handleOnApply} startDate={startDate} endDate={endDate} initialSettings={{
              locale: { format: 'DD-MM-YYYY', separator: '-' },
              maxDate: maxDate,
              showDropdowns: true,
              startDate: startDate, endDate: endDate,
              ranges: {
                Today: [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
                Yesterday: [
                  moment().subtract(1, 'days').format("DD-MM-YYYY"),
                  moment().subtract(1, 'days').format("DD-MM-YYYY"),
                ],
                'Last 7 Days': [
                  moment().subtract(6, 'days').format("DD-MM-YYYY"),
                  moment().format("DD-MM-YYYY"),
                ],
                'Last 30 Days': [
                  moment().subtract(29, 'days').toDate(),
                  moment().format("DD-MM-YYYY"),
                ],
                // 'This Month': [
                //   moment().startOf('month').format("DD-MM-YYYY"),
                //   moment().endOf('month').format("DD-MM-YYYY"),
                // ],
                'Last Month': [
                  moment().subtract(1, 'month').startOf('month').format("DD-MM-YYYY"),
                  moment().subtract(1, 'month').endOf('month').format("DD-MM-YYYY"),
                ],
              },
            }} onCallback={handleCallback}
            >
              <button type="button" className="btn btn-hover-light-secondary btn-sm btn-icon mr-1" id="reportrange">
                <OverlayTrigger
                  placement='bottom'
                  overlay={
                    <Tooltip>Date Filter</Tooltip>
                  }
                >
                  <div className="btn btn-hover-light-secondary btn-sm btn-icon mr-1">
                    <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                      <SVG className="h-75 align-self-end" src={toAbsoluteUrl(
                        "/media/svg/calendar.svg"
                      )}
                      ></SVG>
                    </span>
                    {/* <span className="btn_txt">{startDate} - {endDate}</span> */}
                  </div>
                </OverlayTrigger>
              </button>
            </DateRangePicker>

            {/* <DateRangePicker onApply={handleOnApply}
              startDate={startDate} endDate={endDate}
              initialSettings={{
                locale: { format: 'DD-MM-YYYY', separator: '-' },
                maxDate: maxDate,
                startDate: startDate, endDate: endDate, drops: 'auto', opens: 'left', alwaysShowCalendars: true, showWeekNumbers: true, ranges: {
                  'Today': [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
                  'Yesterday': [moment().subtract(1, 'days').format("DD-MM-YYYY"), moment().subtract(1, 'days').format("DD-MM-YYYY")],
                  'Last 7 Days': [moment().subtract(6, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
                  'Last 30 Days': [moment().subtract(29, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
                  'This Month': [moment().startOf('month').format("DD-MM-YYYY"), moment().endOf('month').format("DD-MM-YYYY")],
                  'Last Month': [moment().subtract(1, 'month').startOf('month').format("DD-MM-YYYY"), moment().subtract(1, 'month').endOf('month').format("DD-MM-YYYY")]
                },
              }}
            >
            <button type="button" className={isPdfDownloading ? `pdf-loading btn btn-hover-light-secondary btn-sm btn-icon mr-1` : `btn btn-hover-light-secondary btn-sm btn-icon mr-1`} disabled={isPdfDownloading ? `disabled` : ''} onClick={pdfClickGenerate}>
              <span className="svg-icon svg-icon-sm svg-icon-dark-75">
                {
                  isPdfDownloading ? <Loader type="ThreeDots" color="#000000" height={18} width={18} style={{ marginRight: 5 }} /> : <SVG className="h-75 align-self-end" src={toAbsoluteUrl(
                    "/media/svg/export.svg"
                  )}
                  ></SVG>
                </span>
                <span className="btn_txt">{startDate} - {endDate}</span>

              </Button>
            </DateRangePicker> */}


            <div className="m_refresh">
              <OverlayTrigger
                placement='bottom'
                overlay={
                  <Tooltip>Refresh</Tooltip>
                }
              >
                <button onClick={onClickRefreshButton} type="button" className="btn btn-hover-light-secondary btn-sm btn-icon" data-toggle="tooltip" data-theme="dark" title="" data-placement="right" data-original-title="Refresh">
                  <span className={`svg-icon svg-icon-sm svg-icon-dark-75 mr-0 ${(wallRefresh && wallRefresh.refreshing) ? `fa fa-spin` : ``}`}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                      <g id="Group_4474" data-name="Group 4474" transform="translate(10951 -4712)">
                        <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10951 4712)" fill="none" opacity="0"></rect>
                        <g id="Group_1" data-name="Group 1" transform="translate(-10950 4715)">
                          <path
                            id="Path_17"
                            data-name="Path 17"
                            d="M17.617,40.968a6.864,6.864,0,0,1-3.843,1.155,6.966,6.966,0,0,1-6.929-6.059H8a1.027,1.027,0,0,0,.869-.474.9.9,0,0,0,0-.967L6.656,30.974a1,1,0,0,0-.869-.474h0a1.027,1.027,0,0,0-.869.474c-.778,1.25-1.933,3.277-2.122,3.556a1.344,1.344,0,0,0-.189.566.916.916,0,0,0,.961.967H4.814a8.994,8.994,0,0,0,8.962,7.993,8.833,8.833,0,0,0,4.9-1.438.955.955,0,0,0,.283-1.344,1.02,1.02,0,0,0-1.342-.307Z"
                            transform="translate(-2.605 -26.138)"
                            fill="#545454"
                          ></path>
                          <path
                            id="Path_18"
                            data-name="Path 18"
                            d="M42.874,19.993h-1.25A8.993,8.993,0,0,0,32.666,12a8.833,8.833,0,0,0-4.9,1.438.971.971,0,1,0,1.061,1.626,6.864,6.864,0,0,1,3.843-1.155A6.966,6.966,0,0,1,39.6,19.969H38.444a1.027,1.027,0,0,0-.869.474.9.9,0,0,0,0,.967l2.213,3.654a1,1,0,0,0,.869.474h0a1.027,1.027,0,0,0,.869-.474c.778-1.25,1.933-3.277,2.122-3.556a1.344,1.344,0,0,0,.189-.566.923.923,0,0,0-.963-.949Z"
                            transform="translate(-21.494 -12)"
                            fill="#545454"
                          ></path>
                        </g>
                      </g>
                    </svg>
                  </span>
                </button>

              </OverlayTrigger>
            </div>



          </div>
          
        </div>

      </div>
    </div >
  );
}
