/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import { connect } from "react-redux";
import { v4 as uuidv4 } from 'uuid';
import SVG from "react-inlinesvg";
import { Button } from "react-bootstrap";
import { toAbsoluteUrl } from "../../_helpers";
import { kFormatter } from '../../../utils'
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import SeeMoreModal from "../see-more-modal/SeeMoreModal";
import MoreActiveUsers from './MoreActiveUsers'
import UserProfileModal from "../user-profile-modal/UserProfileModal";
import { ActiveUserSkeleton } from "../skeleton-loader-cards/SkeletonCards";

class ActiveUsers extends Component {

  state = {
    record_most_active_users: null,
    isLoading: true,
    show: false,
    total: 0,
    isOpen: false,
    userData: null
  }

  componentDidMount() {
    const { record_most_active_users } = this.props;
    if (record_most_active_users && record_most_active_users.data) {

      this.setState({ record_most_active_users: record_most_active_users.data, total: record_most_active_users.total }, () => setTimeout(() => this.setState({ isLoading: false }), 2000))
    }
  }
  static getDerivedStateFromProps(props, state) {
    if (props.record_most_active_users !== state.record_most_active_users) {
      return {
        record_most_active_users: props.record_most_active_users.data,
        total: props.record_most_active_users.total
      };
    }
    return null;
  }

  handleShow = event => {
    this.setState({ show: true });
  }
  handleClose = event => {
    this.setState({ show: false })
  }
  IsOpenUserProfile = (data) => event => this.setState({ isOpen: true, userData: data, show: false });
  IsCloseUserProfile = event => this.setState({ isOpen: false, userData: null });
  render() {
    const { className, analyticsStatus, pageRequest } = this.props;
    const { isLoading, record_most_active_users, total, userData } = this.state;
    return (
      <div className={`card card-custom ${className}`}>
        {pageRequest.loader ? <ActiveUserSkeleton /> : <>
          <div className="card-header min-h-auto py-3">
            <div className="card-title justify-content-between w-100">
              <h5 className="card-label font-weight-bolder">Most Active Creators</h5>
              {((total && parseInt(total) > 5) || analyticsStatus.trialAccessAPI) ? <button type="button" className="btn p-0 see_more_btn text-hover-primary" onClick={this.handleShow}>See More</button> : null}
            </div>
          </div>
          <div className="card-body p-0 mb-5">
            {
              pageRequest.loader ? <DataLoading /> : (record_most_active_users && record_most_active_users.length > 0) ? record_most_active_users.map((mItem, mIndex) => <div className="d-flex px-5 px-xxl-5 py-3 users_row justify-content-between" key={uuidv4()}>
                <div className="d-flex align-items-center">
                  <div className="symbol symbol-45 symbol-light mr-sm-6 mr-3 con_user_img cursor-pointer" onClick={this.IsOpenUserProfile(mItem)}>
                    <span className="no-repeat-fit-background" style={{ backgroundImage: `url(${mItem.image.replace(/\s/g, '')}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span>
                  </div>
                  <div className="d-flex text-left flex-column conn_user_info cursor-pointer" onClick={this.IsOpenUserProfile(mItem)}>
                    <h6 className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name">{mItem.author.name}</h6>
                    <p className="m-0">
                      <small className="text-muted overflow-hidden text-nowrap text-ellipsis post_count mr-2">{kFormatter(mItem.postCount)} {mItem.postCount && parseInt(mItem.postCount) > 1 ? `Posts` : `Post`} </small>
                      <small className="text-muted overflow-hidden text-nowrap text-ellipsis followers_count">{kFormatter(mItem.author.follower)} {mItem.author.follower && parseInt(mItem.author.follower) > 1 ? `Followers` : `Follower`} </small>
                    </p>
                  </div>
                </div>
                <div className="list_category_icon cursor-pointer pt-1" onClick={this.IsOpenUserProfile(mItem)}>
                  <SVG className="align-self-end" src={toAbsoluteUrl(
                    `/media/svg/social-icons/${mItem.network.icon}.svg`
                  )}
                  ></SVG>
                </div>
                <div className="conn_status connnected">
                  <div className="conn_status_wrap">
                    <Button variant="outline-dark btn-min-w" className={analyticsStatus.trialAccessAPI ? `disabled` : ``} size="sm" onClick={!analyticsStatus.trialAccessAPI ? event => window.open(mItem.links, '_blank') : null}>Connect</Button>
                  </div>
                </div>
              </div>
              ) : <DataNotFound />
            }
          </div>
          <SeeMoreModal size={'lg'} showHide={this.state.show} handleClose={this.handleClose} title={'Most Active Creators'}>
            <MoreActiveUsers {...this.props} IsOpenUserProfile={this.IsOpenUserProfile} total={total} />
          </SeeMoreModal>
          {this.state.isOpen ? <UserProfileModal size={'lg'} showHide={this.state.isOpen} handleClose={this.IsCloseUserProfile} title={''} userData={userData} trialAccessAPI={analyticsStatus.trialAccessAPI} /> : null}
        </>}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    record_most_active_users: state.wallRecordMostActiveUsers.record_most_active_users,
    pageRequest: state.wallRecordMostActiveUsers
  }
}
export default connect(mapStateToProps)(ActiveUsers);