import React from "react";
import { Button, Modal } from "react-bootstrap";
import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../_helpers";
import { getPostDetails } from '../../../actions/analytics'
import DataLoading from '../loader'
import { kFormatter } from '../../../utils'
import TextContent from '../textContent'
import { PostModalSkeleton } from "../skeleton-loader-cards/PopupSkeletonCards";
export default class MentionPostsModal extends React.Component {
    state = {
        postData: null
    }
    componentDidMount() {
        const { userData } = this.props;

        if (userData && Object.keys(userData).length > 0) {
            getPostDetails(userData.id).then(({ data }) => {

                this.setState({ postData: data })
            })
                .catch((error) => {
                    console.error("LAYOUT ISSUE", error)
                });
        }

    }
    render() {
        const { postData } = this.state;
        const { show, onHide, size, title, trialAccessAPI } = this.props;

        return (
            <Modal show={show}
                onHide={onHide}
                backdrop="static"
                keyboard={false}
                aria-labelledby="contained-modal-title-vcenter"
                centered
                size={size}>
                <Modal.Header closeButton>
                    <h5 className="m-0">{title}</h5>
                </Modal.Header>
                <Modal.Body className="p-4">

                    {postData ?
                        <div className="mentions_profile_box">
                            {postData.postsCount == 0 ? <div className={`text-center`}> No Data Found </div> :
                            <div className={`mentions_profile_inner d-flex mobile-d-block ${postData.responseData[0].type != 1 ? `` : `p-3`}`}>
                                {}
                                {postData.responseData[0].type != 1 ? <div className="mentions_thumbnail_left w-50 mobile-w-100 mb-lg-0 mb-5">
                                    <div className="mentions_thumbnail_items w-100">
                                        {/* <span className="no-repeat-fit-background" style={{ backgroundImage: `url(${postData.responseData[0].mediaUrl}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span> */}
                                        <img src={postData.responseData[0].mediaUrl ? postData.responseData[0].mediaUrl : 'https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg'} className="img-fluid w-100 bg-light" onError={event => event.target.src = `https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg`} />
                                    </div>
                                </div> : null
                                }
                                <div className={`mentions_profile_right w-${postData.responseData[0].type != 1 ? 50 : 100} pl-lg-5 mobile-w-100 d-flex flex-wrap`}>
                                    <div className="w-100">
                                        <div className="mentions_user_info d-flex">
                                            <div className="mentions_profile_items mr-5">
                                                <span className="no-repeat-fit-background" style={{ backgroundImage: `url(${postData.authorImage.replace(/\s/g, '')}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span>
                                            </div>
                                            <div className="mentions_profile_items_info w-100">
                                                <h6 className="m-0 d-flex align-items-center justify-content-between">
                                                    <span>{postData.authorName}</span>
                                                    <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                        `/media/svg/social-icons/${postData.responseData[0].network.icon}.svg`
                                                    )}
                                                    ></SVG>
                                                </h6>
                                                <p className="m-0"><small>@{postData.authorUsername}</small></p>
                                                <p className="m-0">
                                                    <span className="overflow-hidden text-nowrap text-ellipsis post_count mr-2">
                                                        <strong>{kFormatter(parseInt(postData.postsCount))}</strong> {parseInt(postData.postsCount) > 1 ? `Posts` : `Post`}</span>
                                                    <span className="overflow-hidden text-nowrap text-ellipsis followers_count">
                                                        <strong>{kFormatter(parseInt(postData.followersCount))}</strong> {parseInt(postData.followersCount) > 1 ? `Followers` : `Follower`}</span>
                                                </p>
                                            </div>
                                        </div>
                                        <div className="mt-5">

                                            <TextContent fullContent={postData.responseData[0].content} contentData={postData.responseData[0].content} fullcontentData={postData.responseData[0].content} />
                                            <div className="mentions_profile_data">
                                                <h6>Post Analytics</h6>
                                                <p>
                                                    <span className="w-50 d-inline-block">
                                                        <span className="svg-icon svg-icon-sm svg-icon-dark-75 mr-1">
                                                            <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                `/media/svg/post-icon/impressions.svg`
                                                            )}
                                                            ></SVG>
                                                        </span> Impressions
                                                </span>
                                                    <strong className={`d-inline-block pl-5 text-muted w-50 text-lg-${postData.responseData[0].type != 1 ? `left` : `center`} text-right`}>{kFormatter(parseInt(postData.responseData[0].Impressions))}</strong>
                                                </p>
                                                <p>
                                                    <span className="w-50 d-inline-block">
                                                        <span className="svg-icon svg-icon-sm svg-icon-dark-75 mr-1">
                                                            <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                `/media/svg/post-icon/post_click.svg`
                                                            )}
                                                            ></SVG>
                                                        </span> Post Clicks
                                                </span>
                                                    <strong className={`d-inline-block pl-5 text-muted w-50 text-lg-${postData.responseData[0].type != 1 ? `left` : `center`} text-right`}>{kFormatter(parseInt(postData.responseData[0].postClick))}</strong>
                                                </p>
                                                <p>
                                                    <span className="w-50 d-inline-block">
                                                        <span className="svg-icon svg-icon-sm svg-icon-dark-75 mr-1">
                                                            <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                `/media/svg/post-icon/CTA.svg`
                                                            )}
                                                            ></SVG>
                                                        </span>
                                                    Clicked CTA
                                                </span>
                                                    <strong className={`d-inline-block pl-5 text-muted w-50 text-lg-${postData.responseData[0].type != 1 ? `left` : `center`} text-right`}>{kFormatter(parseInt(postData.responseData[0].postCTA))}</strong>
                                                </p>
                                                <p>
                                                    <span className="w-50 d-inline-block">
                                                        <span className="svg-icon svg-icon-sm svg-icon-dark-75 mr-1">

                                                            <SVG className="h-100 align-self-end" src={toAbsoluteUrl(
                                                                `/media/svg/post-icon/total_reach.svg`
                                                            )}
                                                            ></SVG>
                                                        </span> Total Reach
                                                </span>
                                                    <strong className={`d-inline-block pl-5 text-muted w-50 text-lg-${postData.responseData[0].type != 1 ? `left` : `center`} text-right`}>{kFormatter(parseInt(postData.responseData[0].potentialReach))}</strong>
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                    {(postData.share && Object.keys(postData.share).length > 0) ? <div className="mentions_social_share d-flex justify-content-center w-100 align-items-end">
                                        <span onClick={!trialAccessAPI ? event => window.open(postData.share.facebook, '_blank') : null} className={`cursor-pointer ${trialAccessAPI ? `disabled` : ``}`}>
                                            <i className="fab fa-facebook-f"></i>
                                        </span>
                                        <span onClick={!trialAccessAPI ? event => window.open(postData.share.twitter, '_blank') : null} className={`cursor-pointer ${trialAccessAPI ? `disabled` : ``}`}>
                                            <i className="fab fa-twitter"></i>
                                        </span>
                                        <span onClick={!trialAccessAPI ? event => window.open(postData.share.linkedin, '_blank') : null} className={`cursor-pointer ${trialAccessAPI ? `disabled` : ``}`}>
                                            <i className="fab fa-linkedin-in"></i>
                                        </span>
                                        <span onClick={!trialAccessAPI ? event => window.open(postData.responseData[0].link, '_blank') : null} className={`cursor-pointer ${trialAccessAPI ? `disabled` : ``}`}>
                                            <i className="fa fa-link"></i>
                                        </span>
                                    </div> : null}
                                </div>
                            </div> }
                        </div>
                        : <PostModalSkeleton />}</Modal.Body>
                <Modal.Footer>
                    {(postData && Object.keys(postData).length > 0 && postData.responseData && postData.responseData.length > 0) ? <Button variant="btn btn-primary btn-min-w" size="sm" className={trialAccessAPI ? `disabled` : ``} onClick={!trialAccessAPI ? event => window.open(postData.responseData[0].link, '_blank') : null}>
                        <SVG className="h-75 align-self-end w-20 mr-1" src={toAbsoluteUrl("/media/svg/cheat_icon_white.svg")}></SVG> Connect
                </Button> : null}
                </Modal.Footer>
            </Modal>
        );
    }
}
