import {
    API_GET_WALL_RECORD_TYPE_BY_POST_NETWORK_LOADER,
    GET_WALL_RECORD_TYPE_BY_POST_NETWORK
} from './keys'
const initialState = {
    record_type_by_post_network: [],
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_TYPE_BY_POST_NETWORK: {
            return { ...state, record_type_by_post_network: action.payload }
        }
        case API_GET_WALL_RECORD_TYPE_BY_POST_NETWORK_LOADER: {
            return { ...state, loader: action.payload}
        }
        default:
            return { ...state }
    }

}