import React, { Component } from "react";
import { connect } from "react-redux";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4plugins_wordCloud from "@amcharts/amcharts4/plugins/wordCloud";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import DataLoading from '../loader'
import DataNotFound from '../notfound'
import { WordCloudSkeleton } from "../skeleton-loader-cards/SkeletonCards";

class WordCloud extends Component {
  state = {
    record_word_cloud: null,
    isLoading: true
  }
  componentDidMount() {
    const { record_word_cloud } = this.props;
    if (record_word_cloud) {
      this.setState({ record_word_cloud: record_word_cloud }, () => setTimeout(() => this.onLoadData(), 2000))
    }
  }
  componentDidUpdate(prevProps, prevState) {
    if (this.props.record_word_cloud !== prevState.record_word_cloud) {
      const { record_word_cloud } = this.props;
      this.setState({ record_word_cloud: record_word_cloud }, () => setTimeout(() => this.onLoadData(), 2000))
    }
  }


  onUpdateWordCount = () => {
    this.setState({ isLoading: false });
    const { record_word_cloud } = this.state;
    am4core.useTheme(am4themes_animated);
    let chart = am4core.create("wordCloud", am4plugins_wordCloud.WordCloud);
    let series = chart.series.push(new am4plugins_wordCloud.WordCloudSeries());

    series.accuracy = 4;
    series.step = 15;
    series.rotationThreshold = 0.7;
    series.maxCount = 200;
    series.minWordLength = 2;
    series.fontFamily = "inter, sans-serif";
    series.maxFontSize = am4core.percent(30);
    chart.logo.disabled = true;
    series.colors = new am4core.ColorSet();
    series.colors.passOptions = {}; // makes it loop
    series.angles = [0, -90];
    series.fontWeight = "600"
    series.dataFields.word = "tag";
    series.dataFields.value = "count";
    series.labels.template.propertyFields.fill = "color";
    series.labels.template.tooltipText = "{word}:\n[bold]{value}[/]";
    series.randomness = 0
    series.data = record_word_cloud;
  }
  onLoadData = () => this.onUpdateWordCount()

  render() {
    const { isLoading, record_word_cloud } = this.state;
    const { pageRequest } = this.props
    return <div className="card card-custom card-stretch gutter-b">
      {pageRequest.loader ? <WordCloudSkeleton /> : <>
        <div className="card-header min-h-auto py-3">
          <div className="card-title">
            <h5 className="card-label font-weight-bolder">Word Cloud</h5>
          </div>
        </div>
        <div className="card-body">
          <div className="word_cloud d-flex align-items-center justify-content-center h-100">
          {pageRequest.loader ? <DataLoading /> : (record_word_cloud && record_word_cloud.length > 0) ?
            <div id="wordCloud" style={{ width: "100%", height: "100%", display: (record_word_cloud && record_word_cloud.length > 0) ? `block` : `none` }}></div>
            : record_word_cloud && (record_word_cloud.length == 0 || record_word_cloud == null) ? <DataNotFound /> : null}
          </div>
        </div>
      </>}
    </div>
  }
}
const mapStateToProps = (state) => {
  return {
    record_word_cloud: state.wallRecordWordCloud.record_word_cloud,
    pageRequest: state.wallRecordWordCloud
  }
}
export default connect(mapStateToProps)(WordCloud);