
import React, { Component } from "react";
import Loader from "react-loader-spinner";
import './loader.css'



class DataLoading extends Component {
    render() {
        return <div className="loader-container"><Loader
            type="ThreeDots"
            color="Grey"
            height={50}
            width={50}
        /></div>
    }
}
export default DataLoading