import React from "react";
import { HOST_URL_TAGGSHOP } from '../../actions/api'
const NotFound = () => {
    return <div class="bg-white w-100 h-100">

        <div class="container">
            <div class="vh-100 d-flex align-items-center justify-content-center">
                <div class="error_wrap py-8">
                    <div class="error_img d-flex justify-content-center mb-5 pt-5">
                        <img class="img-fluid" src="https://cloud.taggbox.com/analytics/media/error/plug.svg" />
                    </div>
                    <div class="error_img d-flex justify-content-center mb-5 w-50 m-auto">
                        <img class="img-fluid" src="https://cloud.taggbox.com/analytics/media/error/oops.svg" />
                    </div>
                    <div class="text-center pb-3">
                        <h2 class="font-weight-bold">We searched everywhere!</h2>
                        <p class="text-muted">This page could not be found.</p>
                    </div>
                    <div class="btn_wrap text-center mb-5">
                        <a href={`${HOST_URL_TAGGSHOP}`} class="btn btn-primary btn-min-w">Go Back</a>
                    </div>
                </div>
            </div>
        </div> </div>

}

export default NotFound;