import {
    API_GET_WALL_RECORD_POST_BY_HIGEST_SHARED_LOADER,
    GET_WALL_RECORD_POST_BY_HIGHEST_SHARED
} from './keys'
const initialState = {
    record_post_by_highest_shared: [],
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_POST_BY_HIGHEST_SHARED: {
            return { ...state, record_post_by_highest_shared: action.payload }
        }
        case API_GET_WALL_RECORD_POST_BY_HIGEST_SHARED_LOADER: {
            return {...state, loader: action.payload}
        }
        default:
            return { ...state }
    }

}