
import axios from 'axios';
import { HOST_URL, IS_LIVE } from './api'


const IS = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6NzY2MzYsImVtYWlsSWQiOiJzaGFzaGkuc0B0YWdnYm94LmNvbSIsImlhdCI6MTY2NzU0NTgzNiwiZXhwIjoxNjc1MzIxODM2fQ.NFhtc5-w9ZZkHy13Y-3Y5w0OkPrtkP4P9FHXtg6eRMg'
//const update = `eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6MTI2MCwiZW1haWxJZCI6ImZyaEBlbWFpbC5jb20iLCJpYXQiOjE2MzI5MjAxMDQsImV4cCI6MTY0MDY5NjEwNH0.GUQDbwdxr2mj4Mb3FPfuZME0P4kGVFXIhxHgKXvhUf4`

const requestInterceptor = (config) => {
    var headers = {}
    headers = (IS_LIVE && localStorage.getItem("token")) ? { 'authorization': `Bearer ${localStorage.getItem("token")}` } : { 'authorization': 'Bearer ' + IS, }
    return {
        ...config,
        headers: {
            ...config.headers,
            ...headers

        },
    }
};

class HttpClient {
    constructor() {
        const options = { baseURL: HOST_URL };
        const instance = axios.create(options);
        instance.interceptors.request.use(requestInterceptor);
        axios.interceptors.response.use(
            function (response) {
                return response;
            },
            function (error) {
                return Promise.reject(error);
            }
        );
        return instance;
    }
}
export default HttpClient;
