import {
    GET_WALL_RECORD_COUNTS, GET_WALL_RECORD_COUNTS_LOADER
} from './keys'
const initialState = {
    record_counts: [],
    loader: true
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_WALL_RECORD_COUNTS: {
            return { ...state, record_counts: action.payload }
        }
        case GET_WALL_RECORD_COUNTS_LOADER: {
            return { ...state, loader: action.payload }
            }
        default:
            return { ...state }
    }

}