/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import {
  Tab, Tabs
} from "react-bootstrap";
import { connect } from "react-redux";
import { MentionSharedPostsSkeleton } from "../skeleton-loader-cards/SkeletonCards";
import MentionPosts from './mentionPost'
import SharedPosts from './sharedPost'
class MentionSharedPosts extends Component {
  render() {
    const { className, pageRequest } = this.props;
    return (


      <div className={`card card-custom ${className}`}>
        {pageRequest.loader ? <MentionSharedPostsSkeleton /> :
          <Tabs defaultActiveKey="mentions_posts" id="postgraph" className="nav-tabs-line text-dark font-size-h5 flex-nowrap overflow-x-auto">
            <Tab eventKey="mentions_posts" title="Mentions Posts">
              <div className="card-body p-0 mb-5">
                <MentionPosts {...this.props} />
              </div>
            </Tab>
            <Tab eventKey="shared_posts" title="Highest Post Shared">
              <div className="card-body p-0 mb-5">
                <SharedPosts {...this.props} />
              </div>
            </Tab>
          </Tabs>
        }
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    pageRequest: state.wallRecordPostByMention
  }
}

export default connect(mapStateToProps)(MentionSharedPosts)

