export const ordinal_suffix_of = (i) => {
    var j = i % 10,
        k = i % 100;
    if (j == 1 && k != 11) {
        return i + "st";
    }
    if (j == 2 && k != 12) {
        return i + "nd";
    }
    if (j == 3 && k != 13) {
        return i + "rd";
    }
    return i + "th";
}

export const secondsToTime = (secs) => {
    // var hours = Math.floor(secs / (60 * 60));
    // var divisor_for_minutes = secs % (60 * 60);
    // var minutes = Math.floor(divisor_for_minutes / 60);

    // var divisor_for_seconds = divisor_for_minutes % 60;
    // var seconds = Math.ceil(divisor_for_seconds);
    // return `${hours}:${minutes}:${seconds}`;

    var sec_num = parseInt(secs, 10); // don't forget the second param
    var hours = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    var seconds = sec_num - (hours * 3600) - (minutes * 60);

    if (hours < 10) { hours = "0" + hours; }
    if (minutes < 10) { minutes = "0" + minutes; }
    if (seconds < 10) { seconds = "0" + seconds; }

    return hours + ':' + minutes + ':' + seconds;


}

export const getTimeTypeSecondToTime = (secs, type) => {
    let dataTime = secondsToTime(secs);
    let acrtualTime = dataTime.split(':')
    if (type == 1) {
        if (acrtualTime && acrtualTime.length) return acrtualTime[0];
        else return 0;
    }
    else if (type == 2) {

        if (acrtualTime && acrtualTime.length) return acrtualTime[1];
        else return 0;
    }
    else if (type == 3) {
        if (acrtualTime && acrtualTime.length) return acrtualTime[2];
        else return 0;
    }
}
export const validURL = (str) => {
    var pattern = new RegExp('^(https?:\\/\\/)?' + // protocol
        '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
        '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
        '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
        '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
        '(\\#[-a-z\\d_]*)?$', 'i'); // fragment locator
    return !!pattern.test(str);
}

export const convertIntToBool = (value) => {
    if (value) return Boolean(value)
    else return value;

}
