export const updateURLForIframe = (url) => {
    if (url && url.includes('youtube.com')) {
        const urlSplits = url.split('?v=');
        if (urlSplits && urlSplits.length && urlSplits[1]) {
            url = `https://www.youtube.com/embed/${urlSplits[1]}`;
        }
    }
    else if (url && url.includes('vimeo.com')) {
        const urlSplits = url.split('/');
        if (urlSplits && urlSplits.length && urlSplits[urlSplits.length - 1]) {
            url = `https://player.vimeo.com/video/${urlSplits[urlSplits.length - 1]}?autoplay=1&loop=1&autopause=0`;
        }
    }
    return url;
}

export const getSecondsFromTime = (time) => {
    let actualTime = time.split(':');
    let totalSeconds = (+actualTime[0]) * 60 * 60 + (+actualTime[1]) * 60 + (+actualTime[2]);
    return totalSeconds;
}

export const loaderCircleStyle = { padding: 1, color: '#545454', fontSize: 10 }
export const getPosition = (partitions, currentSceneContent) => {
    const getPartitions = Object.keys(partitions).map((item) => ({ position: item, name: partitions[item] }))
    const filterData = getPartitions.filter((item) => item.name == currentSceneContent.name);
    return filterData;
}
export const GET_HEIGHT_WIDTH = (cssStyle, position, layouts, mixContentType) => {

    const actualLayout = layouts.filter((item) => item.id == mixContentType)

    if (actualLayout && actualLayout[0]) {


        const currentHeightWidth = GET_DYNAMIC_HEIGHT_WIDTH(cssStyle, position);
        const actualDefault = GET_DYNAMIC_HEIGHT_WIDTH(actualLayout[0].css, position);
        if (actualDefault && Object.keys(actualDefault).length) {
            if (parseInt(actualDefault.height) > parseInt(actualDefault.width)) {
                return { width: currentHeightWidth.width, height: 0, label: "Width" }
            }
            else if (parseInt(actualDefault.height) < parseInt(actualDefault.width)) {
                return { height: currentHeightWidth.height, width: 0, label: "Height" }
            }
            else {
                if (mixContentType == 1) {
                    return { height: currentHeightWidth.height, width: 0, label: "Height" }
                }
                else {
                    return { height: 0, width: 0 }
                }
            }
        }
        else {
            return currentHeightWidth;
        }
    }
}

const GET_DYNAMIC_HEIGHT_WIDTH = (cssStyle, position) => {
    const cData = cssStyle.split("#zone");

    var height = ""; var width = "";
    if (cData && cData.length) {
        cData.map((item, index) => {
            const trimContent = item.trim()
            const firstValue = trimContent.charAt(0);
            if (firstValue == position[0].position) {
                const replaceValue = item.split(";")
                if (replaceValue && replaceValue.length) {
                    replaceValue.map((rItem) => {
                        const getHeightWidth = rItem.split(":");
                        if (getHeightWidth && getHeightWidth[0] && getHeightWidth[0] == "height") height = getHeightWidth[1].replace("%", "");
                        if (getHeightWidth && getHeightWidth[0] && getHeightWidth[0] == "width") width = getHeightWidth[1].replace("%", "");
                    })
                }
            }
        })
    }
    return { height, width, }
}

export const convertHMS = (timeString) => {
    const arr = timeString.split(":");
    const seconds = arr[0] * 3600 + arr[1] * 60 + (+arr[2]);
    return seconds;
}




export const getRandomArbitrary = (min, max) => {
    return parseInt(Math.random() * (max - min) + min);
}

export const additionArrayData = (data) => {
    if (data && data.length > 0) {
        return data.reduce((total, num) => total + num)
    }
    else return 0
}

export const kFormatter = (value) => {
    // return Math.abs(num) > 999 ? Math.sign(num) * ((Math.abs(num) / 1000).toFixed(1)) + 'k' : Math.sign(num) * Math.abs(num)


    // return Math.abs(Number(num)) >= 1.0e+9 ? (Math.abs(Number(num)) / 1.0e+9).toFixed(2) + "B" : Math.abs(Number(num)) >= 1.0e+6 ? (Math.abs(Number(num)) / 1.0e+6).toFixed(2) + "M" : Math.abs(Number(num)) >= 1.0e+3 ? (Math.abs(Number(num)) / 1.0e+3).toFixed(2) + "K" : Math.abs(Number(num));
    try {


        var newValue = value;
        if (value >= 1000) {
            var suffixes = ["", "K", "M", "B", "T"];
            var suffixNum = Math.floor(("" + value).length / 3);
            var shortValue = '';
            for (var precision = 2; precision >= 1; precision--) {
                shortValue = parseFloat((suffixNum != 0 ? (value / Math.pow(1000, suffixNum)) : value).toPrecision(precision));
                var dotLessShortValue = (shortValue + '').replace(/[^a-zA-Z 0-9]+/g, '');
                if (dotLessShortValue.length <= 2) { break; }
            }
            if (shortValue % 1 != 0) shortValue = shortValue.toFixed(1);
            newValue = shortValue + suffixes[suffixNum];
        }
        return newValue;
    }
    catch (ex) {
        return Math.abs(value) > 999 ? Math.sign(value) * ((Math.abs(value) / 1000).toFixed(1)) + 'k' : Math.sign(value) * Math.abs(value)
    }

}
export const OverviewChartFormatting = (formattingData) => {
    let data = [];
    if (formattingData && formattingData.length > 0) {
        formattingData.map((item) => {
            data.push({
                name: item.name,
                type: 'line',
                data: dataConvertToInt(item.data)
            })
        })
        return data;
    }
    else return formattingData;
}

export const dataConvertToInt = (formateIntData) => {
    let updateData = [];
    if (formateIntData && formateIntData.length > 0) {
        formateIntData.map((item) => {
            updateData.push(parseInt(item))

        })
        return updateData


    }
    else return formateIntData

}


