import React, { useMemo, useEffect, Component } from "react";
import ApexCharts from "apexcharts";
import SVG from "react-inlinesvg";
import {
    Tooltip,
    OverlayTrigger,
} from "react-bootstrap";
import { toAbsoluteUrl } from "../../_helpers";
import { additionArrayData } from '../../../utils'
class ChartPostOverview extends Component {
    componentDidMount() {
        const { index, item, color } = this.props;
        const ID = `post_overview_chart${index}`
        const element = document.getElementById(ID);
        if (!element) {
            return;
        }
        const options = {
            series: [
                {
                    name: item.name,
                    data: item.data
                }
            ],
            chart: {
                type: "area",
                height: 50,
                toolbar: {
                    show: false
                },
                zoom: {
                    enabled: false
                },
                sparkline: {
                    enabled: true
                }
            },
            plotOptions: {},
            legend: {
                show: false
            },
            dataLabels: {
                enabled: false
            },
            fill: {
                type: "gradient",
                gradient: {
                    shadeIntensity: 1,
                    opacityFrom: 0.7,
                    opacityTo: 0.9,
                    stops: [0, 90, 100]
                }
            },
            stroke: {
                curve: "smooth",
                show: true,
                width: 2,
                colors: [color[index]]
            },
            xaxis: {
                categories: item.labels,
                axisBorder: {
                    show: false
                },
                axisTicks: {
                    show: false
                },
                labels: {
                    show: false,
                    style: {
                        colors: color[index],
                        fontSize: "12px",

                    }
                },
                crosshairs: {
                    show: false,
                    position: "front",
                    stroke: {
                        color: color[index],
                        width: 1,
                        dashArray: 3
                    }
                }
            },
            yaxis: {
                labels: {
                    show: false,
                    style: {
                        colors: color[index],
                        fontSize: "12px",

                    }
                }
            },
            states: {
                normal: {
                    filter: {
                        type: "none",
                        value: 0
                    }
                },
                hover: {
                    filter: {
                        type: "none",
                        value: 0
                    }
                },
                active: {
                    allowMultipleDataPointsSelection: false,
                    filter: {
                        type: "none",
                        value: 0
                    }
                }
            },
            colors: [color[index]],
            markers: {
                colors: [color[index]],
                strokeColor: [color[index]],
                strokeWidth: 3
            },
            tooltip: {
                fixed: {
                    enabled: true,
                    position: 'topLeft',
                    offsetX: 0,
                    offsetY: -65,
                },
            }
        };
        const chart = new ApexCharts(element, options);
        chart.render();
    }

    render() {
        const { item, index } = this.props;
        return <><div key={`chartrecord_post_overview_${index}`} className="d-flex align-items-center justify-content-between border-bottom px-5 px-xxl-10 py-3 hover-secondary">
            <div className="pl-0">
                <div className="d-flex align-items-center">
                    <span className="text-dark font-weight-bolder font-size-lg mr-2">
                        {(item.name && item.name.length > 0 && item.name.toLowerCase().includes("widget")) ? `Galleries View` : item.name}
                    </span>
                    <OverlayTrigger
                        placement='right'
                        overlay={
                            <Tooltip> {`${(item.name && item.name.length > 0) ? item.tool_tip : ''}`}</Tooltip>
                        }
                    >
                        <SVG className="h-75 align-self-end"
                            src={toAbsoluteUrl(
                                "/media/svg/question.svg"
                            )}
                        ></SVG>
                    </OverlayTrigger>
                </div>
            </div>
            <div className="text-right">
                <div
                    id={`post_overview_chart${index}`}
                    className="card-rounded-bottom"
                    style={{ height: "60px", width: "120px" }}
                ></div>
            </div>
            <div className="text-right pr-0">
                <h5 className="text-dark font-weight-boldest mb-0">{additionArrayData(item.data)}</h5>
            </div>
        </div>
        </>
    }
}
export default ChartPostOverview;