import React, { Component } from "react";
const findHaseTagIndex = (data) => {
    let FindHasheTag = [];
    for (var i = 0; i < data.length; i++) {
        if (data[i] === "#") FindHasheTag.push(i);
    }
    return FindHasheTag;
}
const findWhiteSpace = (startFrom, content) => {
    let value = 0;

    for (; startFrom < content.length; startFrom++) {
        if (content[startFrom] == " " || content[startFrom] == '') {
            return value = startFrom; break;
        }
    }
    if (value === 0 && content.length < 210) value = content.length;
    else value = 200;
    return startFrom == undefined ? 200 : value;

}
class TextContent extends Component {
    state = {
        isTrimEnable: false
    }
    onClickReadText = event => {
        const { isTrimEnable } = this.state;
        this.setState({ isTrimEnable: !isTrimEnable });
    }
    render() {
        const { contentData, fullContent, fullcontentData } = this.props;
        var content = fullContent;
        let hashTagFindContent = ""; let fullcontent = fullContent;
        if (fullContent.length > 200) {
            hashTagFindContent = String(contentData).substring(0, 200);
            let lastHashTag = findHaseTagIndex(hashTagFindContent);
            let hashTagLimit = findWhiteSpace(lastHashTag[lastHashTag.length - 1], fullContent)
            content = String(contentData).substring(0, parseInt(hashTagLimit));
            fullcontent = String(fullcontentData).substring(parseInt(hashTagLimit), fullContent.length);

        }
        else {
            content = fullContent;
            fullcontent = fullContent;
        }
        return <p style={{ wordBreak: 'break-word' }}>
            {
                content
            }
            {
                fullContent.length > 200 ? <span className="morecontent readMore"><span>{this.state.isTrimEnable ? fullcontent : null}
                    {this.state.isTrimEnable ? <a className="lesslink" onClick={this.onClickReadText}> Read Less</a> : null}</span>
                    {!this.state.isTrimEnable ? <a className="morelink" onClick={this.onClickReadText}> Read More...</a> : null}</span> : null
            }
        </p>

    }
}
export default TextContent;