import React, { createContext, useContext, useState, useEffect } from "react";
import { shallowEqual, useSelector, useDispatch } from "react-redux";

const MetronicSplashScreenContext = createContext();

export function MetronicSplashScreenProvider({ children }) {
  const [count, setCount] = useState(0);
  let visible = count > 0;
  useEffect(() => {
    const splashScreen = document.getElementById("splash-screen");
    const splashScreenBody = document.getElementById("kt_body");
    // Show SplashScreen
    if (splashScreen && visible) {
      splashScreen.classList.remove("hidden");
      splashScreenBody.classList.remove("page-load");

      return () => {
        splashScreen.classList.add("hidden");
        splashScreenBody.classList.add("page-load");
      };
    }



    // Hide SplashScreen
    let timeout;
    if (splashScreen && !visible) {
      timeout = setTimeout(() => {
        splashScreen.classList.add("hidden");
        splashScreenBody.classList.add("page-load");
      }, 3000);
    }

    return () => {
      clearTimeout(timeout);
    };
  }, [visible]);

  return (
    <MetronicSplashScreenContext.Provider value={setCount}>
      {children}
    </MetronicSplashScreenContext.Provider>
  );
}

export function LayoutSplashScreen({ visible = true }) {
  // Everyth are ready - remove splashscreen
  const setCount = useContext(MetronicSplashScreenContext);

  useEffect(() => {
    if (!visible) {
      return;
    }

    setCount(prev => {
      return prev + 1;
    });

    return () => {
      setCount(prev => {
        return prev - 1;
      });
    };
  }, [setCount, visible]);

  return null;
}
